import { Pipe, PipeTransform } from '@angular/core';
import { objectsHasValueByKey } from '../../helpers/object-helpers';

@Pipe({
  name: 'hasValue',
})
export class HasValuePipe implements PipeTransform {
  transform(
    data: { [key: string]: any } | undefined,
    compareTo: { [key: string]: any } | undefined,
    keys: string[]
  ): boolean {
    return Boolean(
      objectsHasValueByKey(data, keys) || objectsHasValueByKey(compareTo, keys)
    );
  }
}
