import { Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { Observable, ReplaySubject } from 'rxjs';
import {
  StopChecksHttpService as StopChecksService,
  StopChecksGetResponse,
  StopCheckPostResponse,
  StopCheckPostRequest,
} from '@backbase/data-ang/stop-checks';
import { distinctUntilChanged, finalize, map } from 'rxjs/operators';
import { fromResponse } from '../helpers/http-helpers';

@Injectable()
export class ChecksService {
  private readonly loadingSub$ = new ReplaySubject<boolean>(1);
  readonly loading$ = this.loadingSub$.pipe(distinctUntilChanged());

  private set loading(val: boolean) {
    this.loadingSub$.next(val);
  }

  constructor(
    @Optional() protected readonly model: ItemModel,
    private readonly stopChecksDataHttpService: StopChecksService
  ) {}

  getStopChecks(params: object): Observable<StopChecksGetResponse> {
    this.loading = true;
    return this.stopChecksDataHttpService
      .getStopChecks(params, 'response')
      .pipe(
        map(fromResponse),
        finalize(() => {
          this.loading = false;
        })
      );
  }

  postStopChecks(
    payload: StopCheckPostRequest
  ): Observable<StopCheckPostResponse> {
    this.loading = true;
    return this.stopChecksDataHttpService
      .postStopChecks({ stopCheckPostRequest: payload }, 'response')
      .pipe(
        map(fromResponse),
        finalize(() => {
          this.loading = false;
        })
      );
  }
}
