import {
  Component,
  Input,
  Output,
  EventEmitter,
  forwardRef,
} from '@angular/core';
import {
  FormControl,
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { CountriesMap } from '../../constants';

@Component({
  selector: 'bb-select-country-dropdown',
  templateUrl: './select-country-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => SelectCountryDropdownComponent),
    },
  ],
})
export class SelectCountryDropdownComponent implements ControlValueAccessor {
  _countries!: Array<string>;
  _selectedCountry = '';
  search = new FormControl();
  countryEntries!: Array<[string, string]>;
  filteredCountries: Array<string> = [];

  /**
   * Array of country iso codes
   */
  @Input() set countries(countries: Array<string>) {
    this._countries = countries || [];
    this.setupCountries();
  }
  /**
   * Selected country iso code
   */
  @Input() selectedCountry = '';
  /**
   * Helper text for the component. Defaults to empty string.
   */
  @Input() helperText = '';
  /**
   * Placeholder text. Defaults to empty string.
   */
  @Input() placeholder = '';
  /**
   * Label of the component. Defaults to empty string.
   */
  @Input() label = '';
  /**
   * Placeholder fot search. Defaults to empty string.
   */
  @Input() searchPlaceholder = '';
  /**
   * Icon for the dropdown. Defaults to `toggle-down`
   */
  @Input() dropdownIcon = 'toggle-down';
  /**
   * Flag which indicates permission to create closed payment
   * Default value is `false`
   */
  @Input() isClosedPayment = false;
  /**
   * Emits value when a country is selected
   */
  @Output() selectCountry = new EventEmitter<string>();

  setupCountries() {
    this.countryEntries = this._countries.map((country: string) => [
      country,
      CountriesMap[country],
    ]);
    this.filteredCountries = [...this._countries];
  }

  selectItem(country: string) {
    this.selectedCountry = country;
    this.selectCountry.emit(country);
    if (this.search.value) {
      this.search.reset();
    }
  }

  handleSearch(query = '') {
    if (query) {
      this.filteredCountries = this.countryEntries.reduce(
        (acc: Array<string>, [key, country]) => {
          if (
            key.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1 ||
            country.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1
          ) {
            acc.push(key);
          }
          return acc;
        },
        []
      );
    } else {
      this.filteredCountries = this._countries;
    }
  }

  onChange() {}
  onTouched() {}
  writeValue(): void {}

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
}
