export const getConfig = (
  configName: string,
  parent?:
    | PaymentTypeConfig
    | PaymentFormGroup
    | Array<PaymentFormGroup | PaymentFormFieldConfig>
): PaymentFormGroup | PaymentFormFieldConfig | undefined => {
  let fields: Array<PaymentFormGroup | PaymentFormFieldConfig> = [];
  if (!configName) {
    return;
  }
  if (Array.isArray(parent)) {
    fields = [...parent];
  } else if (parent?.name === configName) {
    return parent;
  } else {
    fields = parent ? [...parent.fields] : [];
  }
  let config: PaymentFormGroup | PaymentFormFieldConfig | undefined;
  while (fields.length) {
    config = fields.shift();
    if (config) {
      if (config.name === configName) {
        return config;
      } else if ((config as PaymentFormGroup).fields) {
        fields.push(...((<PaymentFormGroup>config) as PaymentFormGroup).fields);
      }
    }
  }

  return;
};

export const isFormGroupConfig = (
  config: PaymentFormGroup | PaymentFormFieldConfig
) => Array.isArray((<PaymentFormGroup>config).fields);

export const isAdditionsGroup = (group: PaymentFormGroup) =>
  group.name === PaymentBaseFields.additions ||
  group.name === CounterPartyFields.counterpartyAccountAdditions;

export function getLabelFromPaymentConfig(
  name: string,
  paymentConfig: PaymentTypeConfig,
  parentName = ''
): string {
  const parentConfig = parentName
    ? <PaymentFormGroup>getConfig(parentName, paymentConfig)
    : paymentConfig;
  const config = getConfig(name, parentConfig);
  let label = '';
  if (config) {
    if (isFormGroupConfig(config)) {
      label = (<PaymentFormGroup>config).title || '';
    } else if (config.options) {
      label = config.options.label || '';
    }
  }
  return label;
}

export const cloneFormFieldOptions = (options: { [key: string]: any } = {}) => {
  const newOptions = { ...options };
  for (const key in newOptions) {
    if (Array.isArray(newOptions[key])) {
      newOptions[key] = [...newOptions[key]];
    } else if (typeof newOptions[key] === 'object') {
      newOptions[key] = { ...newOptions[key] };
    }
  }
  return newOptions;
};

export const clonePaymentFormItem = (
  formItemConfig: PaymentFormGroup | PaymentFormFieldConfig
): PaymentFormGroup | PaymentFormFieldConfig => {
  const configItem: PaymentFormGroup | PaymentFormFieldConfig = {
    ...formItemConfig,
  };
  if ((formItemConfig as PaymentFormGroup)?.fields?.length) {
    (configItem as PaymentFormGroup).fields = (
      formItemConfig as PaymentFormGroup
    ).fields.map(clonePaymentFormItem);
  } else {
    configItem.options = cloneFormFieldOptions(formItemConfig.options);
  }

  return configItem;
};

export const clonePaymentConfig = (
  config: PaymentTypeConfig
): PaymentTypeConfig => ({
  ...config,
  fields: config.fields.map(clonePaymentFormItem),
});

export function setFrequencyOptionsPerConfig(
  config: PaymentTypeConfig,
  isRecurring = false,
  scheduleConfigOptions: {
    isOneOffToRecurrentAllowed?: boolean;
    isRecurrentToOneOffAllowed?: boolean;
  }
): PaymentTypeConfig {
  const clonedConfig = clonePaymentConfig(config);

  const schedule = getConfig(
    PaymentBaseFields.schedule,
    clonedConfig
  ) as PaymentFormGroup;
  const frequencyFieldOptions = getConfig(ScheduleFields.frequency, schedule)
    ?.options as PaymentFormFieldConfig['options'];

  if (isRecurring && !scheduleConfigOptions.isRecurrentToOneOffAllowed) {
    frequencyFieldOptions?.options.shift();
  } else if (
    !isRecurring &&
    !scheduleConfigOptions.isOneOffToRecurrentAllowed
  ) {
    frequencyFieldOptions.options = [frequencyFieldOptions?.options[0]];
  }

  return { ...clonedConfig };
}

export function removeScheduleFromConfig(
  config: PaymentTypeConfig
): PaymentTypeConfig {
  const clonedConfig = clonePaymentConfig(config);
  return {
    ...clonedConfig,
    // Schedule is on the top level so we can skip deep nested checks.
    fields: clonedConfig.fields.filter(
      (field) => field.name !== PaymentBaseFields.schedule
    ),
  };
}

export const shouldLoadTabContent = (tab: string, options: Options): boolean =>
  !options.tabs || (Array.isArray(options.tabs) && options.tabs.includes(tab));

export function getIconByPaymnentType(paymentType: string): string {
  const type = normalizePaymentType(paymentType);

  switch (type) {
    case INTERNAL_TRANSFER.paymentType:
    case INTRABANK_TRANSFER.paymentType:
      return PaymentTypeIcon.INTERNAL;

    case INTERNATIONAL_TRANSFER.paymentType:
    case 'US_FOREIGN_WIRE':
      return PaymentTypeIcon.INTERNATIONAL;

    case SEPA.paymentType:
    case 'SEPA_CT':
    case US_DOMESTIC_WIRE.paymentType:
    case US_ACH_CREDIT.paymentType:
    case CHAPS.paymentType:
    case UK_FASTER_PAYMENT.paymentType:
      return PaymentTypeIcon.DOMESTIC;
  }
  return '';
}

export function normalizePaymentType(
  paymentType: string | undefined
): string | undefined {
  const paymentTypeSuffixes = ['_ILE', '_CLOSED'];
  if (paymentType) {
    let normalisedPaymentType = paymentType;
    paymentTypeSuffixes.forEach((suffix) => {
      if (paymentType.endsWith(suffix)) {
        normalisedPaymentType = paymentType.replace(suffix, '');
      }
    });

    return normalisedPaymentType;
  }
  return;
}

export function hasCreatePermission(
  item: PaymentTemplate | Array<Action | PaymentAction> | undefined
): boolean {
  const actions = (item as PaymentTemplate)?.actions || (item as Array<Action>);
  return !!actions?.find((action) => action === PaymentTemplateAction.CREATE);
}

export function canCopyPayment(
  item: IdentifiedPaymentOrder | undefined
): boolean {
  return !!item?.permissibleActions?.create;
}

function isModified(currentValue = '', expectedValue = ''): boolean {
  if (!currentValue) {
    return false;
  }
  return currentValue !== expectedValue;
}

function getBeneficairyDetails(
  beneficiary?: Partial<ContactItem | ArrangementItem>
): Partial<ArrangementItem> {
  return {
    bankBranchCode:
      (<ContactItem>beneficiary)?.accounts?.[0]?.bankCode ||
      (<ArrangementItem>beneficiary)?.bankBranchCode,
    accountNumber:
      (<ContactItem>beneficiary)?.accounts?.[0]?.accountNumber ||
      (<ArrangementItem>beneficiary)?.accountNumber,
    name: beneficiary?.name,
    BIC:
      (<ContactItem>beneficiary)?.accounts?.[0]?.BIC ||
      (<ArrangementItem>beneficiary)?.BIC,
  };
}

export function isSelectedBeneficiaryModified(
  counterparty?: CounterPartyDetails,
  selectedBeneficiary?: Partial<ContactItem | ArrangementItem>
): boolean {
  const beneficiaryDetails = getBeneficairyDetails(selectedBeneficiary);
  return (
    (isObjectTruthy(beneficiaryDetails) &&
      isModified(counterparty?.name, beneficiaryDetails.name)) ||
    isModified(
      stripSpaces(counterparty?.accountNumber),
      stripSpaces(beneficiaryDetails.accountNumber)
    ) ||
    isModified(
      counterparty?.creditorBank?.bankBranchCode,
      beneficiaryDetails.bankBranchCode
    ) ||
    isModified(counterparty?.creditorBank?.bic, beneficiaryDetails.BIC)
  );
}

export const getCounterpartySchemeName = (
  paymentItem: PaymentDetails
): SchemeNames => {
  const { counterparty, selectedBeneficiary } = paymentItem;
  const isBeneficiaryModified = isSelectedBeneficiaryModified(
    counterparty,
    selectedBeneficiary
  );
  let creditorScheme = counterparty.schemeName || SchemeNames.BBAN;
  const beneficiarySchemes = [SchemeNames.BBAN, SchemeNames.IBAN];
  const isOwnAccountTransfer =
    selectedBeneficiary?.isArrangement &&
    beneficiarySchemes.includes(creditorScheme);

  // Set scheme to ID when payment template has counterparty id
  if (
    !selectedBeneficiary &&
    counterparty.id &&
    creditorScheme !== SchemeNames.EXTERNALID
  ) {
    creditorScheme = SchemeNames.ID;
  }

  return isOwnAccountTransfer && !isBeneficiaryModified
    ? SchemeNames.ID
    : creditorScheme;
};
