import { Inject, Injectable, Optional } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { BB_PAYMENT_CONFIG, BB_REGISTERED_PAYMENT_CONFIGS } from '../constants';
import {
  OmniPaymentConfigItem,
  PaymentTypeConfig,
} from '../models/form-items.model';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { normalizePaymentType } from '../../ui/helpers/payment-config-helpers';

const DEFAULT_KEY = 'DEFAULT';

@Injectable()
export class PaymentConfigService {
  private readonly paymentConfigRegistry: BehaviorSubject<
    Map<string, PaymentTypeConfig>
  >;
  private readonly configRegistry: BehaviorSubject<
    Map<string, OmniPaymentConfigItem>
  >;
  private readonly classIdRegistry: BehaviorSubject<Map<string, string>>;

  constructor(
    @Inject(BB_PAYMENT_CONFIG)
    @Optional()
    configs: Array<OmniPaymentConfigItem>,
    @Inject(BB_REGISTERED_PAYMENT_CONFIGS)
    @Optional()
    registeredPaymentConfigs: Array<PaymentTypeConfig[]>
  ) {
    this.paymentConfigRegistry = new BehaviorSubject(
      new Map<string, PaymentTypeConfig>(
        configs
          ? configs.reduce(
              (
                acc: any[],
                { paymentType, businessFunction }: OmniPaymentConfigItem
              ) => {
                const paymentConfig = Object.assign(paymentType, {
                  businessFunction,
                });
                return [...acc, [paymentConfig.paymentType, paymentConfig]];
              },
              []
            )
          : undefined
      )
    );

    this.classIdRegistry = new BehaviorSubject(
      new Map<string, string>(
        configs
          ? configs.reduce(
              (acc: any[], { classId, paymentType }) => [
                ...acc,
                [classId || DEFAULT_KEY, paymentType.paymentType],
              ],
              []
            )
          : undefined
      )
    );

    this.configRegistry = new BehaviorSubject(
      new Map<string, OmniPaymentConfigItem>(
        configs
          ? configs.reduce(
              (acc: any[], config: OmniPaymentConfigItem) => [
                ...acc,
                [config.classId || DEFAULT_KEY, config],
              ],
              []
            )
          : undefined
      )
    );

    if (registeredPaymentConfigs && registeredPaymentConfigs.length) {
      registeredPaymentConfigs
        .reduce((acc, val) => acc.concat(val), [])
        .forEach((config: PaymentTypeConfig) =>
          this.setPaymentConfigByType(config)
        );
    }
  }

  has(paymentType: string): boolean {
    return this.paymentConfigRegistry.value.has(paymentType);
  }

  hasClassId(classId: string): boolean {
    return this.classIdRegistry.value.has(classId);
  }

  getPaymentConfigByClassId(classId: string): PaymentTypeConfig | undefined {
    const paymentConfigType = this.getBaseConfigByClassId(classId);
    return paymentConfigType && paymentConfigType.paymentType;
  }

  getPaymentConfigByType(paymentType: string): PaymentTypeConfig | undefined {
    return this.paymentConfigRegistry.value.get(paymentType);
  }

  mapPaymentType(item: PaymentTemplate | string): string | undefined {
    const paymentType =
      typeof item === 'string'
        ? item
        : (item as PaymentTemplate).details.paymentType;
    const _paymentType = normalizePaymentType(paymentType);

    return _paymentType
      ? this.getPaymentConfigByType(_paymentType)?.name
      : undefined;
  }

  getBaseConfigByClassId(classId: string): OmniPaymentConfigItem | undefined {
    return (
      this.configRegistry.value.get(classId) ||
      this.configRegistry.value.get(DEFAULT_KEY)
    );
  }

  setPaymentConfigByType(config: PaymentTypeConfig) {
    const _value = this.paymentConfigRegistry.value;
    _value.set(config.paymentType, config);
    this.paymentConfigRegistry.next(_value);
  }
}
