import {
  Schedule,
  PaymentOrdersValidatePost,
  SchemeNames,
  PurposeOfPaymentInfo,
  IdentifiedPaymentOrder,
} from '@backbase/data-ang/payment-order';

import { FormGroup, AbstractControl } from '@angular/forms';
import { PaymentTemplatesPostRequest } from '@backbase/data-ang/payment-template';
import {
  InitiatePaymentOptions,
  PaymentDetails,
  PaymentFormFieldConfig,
  BaseObject,
} from '../../configs/models/form-items.model';
import {
  CounterPartyFields,
  HiddenFields,
  InitiatorFields,
  PaymentBaseFields,
  PaymentOptionFields,
  PaymentTemplateFields,
  RemittanceInfoFields,
  ScheduleFields,
} from '../../configs/models/form-item-names.model';
import { TemplateParams } from '../models';
import { getCounterpartySchemeName } from '../helpers/payment-config-helpers';
import {
  endTypes,
  ScheduleItem,
} from '../components/schedule/schedule.component';
import { datePart, scheduleOn } from '../helpers/date-helpers';
import { isObjectTruthy, stripSpaces } from '../helpers/type-helpers';

const normalizePaymentSchedule = (scheduleItem: ScheduleItem): Schedule => {
  const {
    frequency,
    endDate,
    startDate = '',
    repeat,
    endType,
    every,
  } = scheduleItem || {};
  const freq = frequency?.value || frequency;
  return {
    transferFrequency: freq as Schedule['transferFrequency'],
    every: (every || '1') as Schedule['every'],
    on: scheduleOn(freq as string, new Date(startDate)),
    startDate: datePart(startDate),
    ...(endType === 'on' && endDate && { endDate: datePart(endDate) }),
    ...(endType === 'after' && repeat && { repeat }),
  };
};

//nosonar
export const toPostRequest = (
  paymentItem: PaymentDetails
): PaymentOrdersValidatePost => {
  if (
    paymentItem[PaymentBaseFields.counterparty][CounterPartyFields.role] ===
    'DEBTOR'
  ) {
    paymentItem = switchPaymentParties(paymentItem);
  }

  const {
    [PaymentBaseFields.counterparty]: {
      [CounterPartyFields.standardEntryClass]: entryClass,
      [CounterPartyFields.creditorBank]: creditorBankDetails,
      [CounterPartyFields.bankName]: bankNameValue,
      [CounterPartyFields.correspondentBank]: correspondentBankDetails,
      [CounterPartyFields.intermediaryBank]: intermediaryBankDetails,
      [CounterPartyFields.recipientId]: recipientId,
      [CounterPartyFields.accountType]: accountType,
      [CounterPartyFields.accountNumber]: accountNumber,
      [CounterPartyFields.name]: counterpartyNameValue,
      [CounterPartyFields.id]: counterpartyId,
      [CounterPartyFields.postalAddress]: postalAddress,
      [CounterPartyFields.messageToBank]: messageToBank,
      [CounterPartyFields.targetCurrency]: targetCurrency,
      [CounterPartyFields.remittanceInformation]: remittanceInfo,
      [CounterPartyFields.endToEndIdentification]: endToEndIdentification,
      [CounterPartyFields.mandateIdentifier]: mandateIdentifier,
      [CounterPartyFields.role]: role,
      [CounterPartyFields.selectedContact]: selectedContact,
      [CounterPartyFields.additions]: creditorAdditions,
      [CounterPartyFields.counterpartyAccountAdditions]:
        counterpartyAccountAdditions,
    },
    [PaymentBaseFields.initiator]: debitorAccount,
    [PaymentBaseFields.remittanceInfo]: {
      [RemittanceInfoFields.urgent]: urgent,
      [RemittanceInfoFields.amountCurrencyGroup]: {
        amount: amountValue,
        currency,
      },
      [RemittanceInfoFields.paymentReference]: paymentReference,
      [RemittanceInfoFields.purposeOfPayment]: purposeOfPayment,
      [RemittanceInfoFields.description]: description,
    },
    [PaymentBaseFields.schedule]: paymentSchedule,
    paymentType,
    approved,
    chargeBearer,
    transferFee,
    [PaymentBaseFields.additions]: additions,
  } = paymentItem;
  const scheduleItem: Schedule = normalizePaymentSchedule(paymentSchedule);

  const {
    [CounterPartyFields.bankBranchCode]: bankBranchCode,
    [CounterPartyFields.bic]: bankBic,
    [CounterPartyFields.postalAddress]: bankAddress,
    [CounterPartyFields.bankName]: creditorBankName,
    [CounterPartyFields.additions]: creditorBankAdditions,
  } = creditorBankDetails || ({} as any);

  const bankName = bankNameValue || creditorBankName;
  const transferFeeValue = transferFee || {};
  const {
    [CounterPartyFields.bankBranchCode]: correspondentBankBranchCode,
    [CounterPartyFields.bic]: correspondentBankBic,
    [CounterPartyFields.postalAddress]: correspondentBankAddress,
    [CounterPartyFields.bankName]: correspondentBankName,
    [CounterPartyFields.additions]: correspondentBankAdditions,
  } = correspondentBankDetails || ({} as any);

  const counterpartyBank = {
    ...(bankBranchCode ? { bankBranchCode } : null),
    ...(bankBic ? { bic: bankBic } : null),
    ...(isObjectTruthy(bankAddress) ? { postalAddress: bankAddress } : null),
    ...(bankName ? { name: bankName } : null),
    ...(isObjectTruthy(creditorBankAdditions)
      ? { additions: creditorBankAdditions }
      : null),
  };

  const {
    [CounterPartyFields.bankBranchCode]: intermediaryBankBranchCode,
    [CounterPartyFields.bic]: intermediaryBankBic,
    [CounterPartyFields.postalAddress]: intermediaryBankAddress,
    [CounterPartyFields.bankName]: intermediaryBankName,
    [CounterPartyFields.additions]: intermediaryBankAdditions,
  } = intermediaryBankDetails || ({} as any);

  const correspondentBank = {
    ...(correspondentBankBranchCode
      ? { bankBranchCode: correspondentBankBranchCode }
      : null),
    ...(correspondentBankBic ? { bic: correspondentBankBic } : null),
    ...(isObjectTruthy(correspondentBankAddress)
      ? { postalAddress: correspondentBankAddress }
      : null),
    ...(correspondentBankName ? { name: correspondentBankName } : null),
    ...(isObjectTruthy(correspondentBankAdditions)
      ? { additions: correspondentBankAdditions }
      : null),
  };

  const intermediaryBank = {
    ...(intermediaryBankBranchCode
      ? { bankBranchCode: intermediaryBankBranchCode }
      : null),
    ...(intermediaryBankBic ? { bic: intermediaryBankBic } : null),
    ...(isObjectTruthy(intermediaryBankAddress)
      ? { postalAddress: intermediaryBankAddress }
      : null),
    ...(intermediaryBankName ? { name: intermediaryBankName } : null),
    ...(isObjectTruthy(intermediaryBankAdditions)
      ? { additions: intermediaryBankAdditions }
      : null),
  };

  const remittanceInformation = remittanceInfo || description;

  const scheduledPayment =
    scheduleItem && scheduleItem.transferFrequency !== 'ONCE'
      ? { paymentMode: 'RECURRING', schedule: { schedule: scheduleItem } }
      : { paymentMode: 'SINGLE', schedule: {} };

  const purposeType = paymentItem.inputFormFromSettings?.purposesOfPayment
    ?.length
    ? 'code'
    : 'freeText';
  const creditorScheme = getCounterpartySchemeName(paymentItem);

  return {
    paymentType,
    ...(approved ? { approved } : {}),
    originatorAccount: {
      identification: {
        identification: debitorAccount.id,
        schemeName: 'ID',
      },
      name: debitorAccount.name,
      ...(debitorAccount.additions
        ? { additions: debitorAccount.additions }
        : {}),
    },
    requestedExecutionDate: datePart(paymentSchedule?.startDate),
    paymentMode:
      scheduledPayment.paymentMode as PaymentOrdersValidatePost['paymentMode'],
    ...(entryClass ? { entryClass } : {}),
    instructionPriority: urgent ? 'HIGH' : 'NORM',
    ...scheduledPayment.schedule,
    transferTransactionInformation: {
      instructedAmount: {
        amount: amountValue,
        currencyCode: currency,
      },
      counterparty: {
        name: counterpartyNameValue,
        ...(recipientId ? { recipientId } : {}),
        ...(isObjectTruthy(postalAddress) ? { postalAddress } : {}),
        ...(role ? { role } : {}),
        ...(isObjectTruthy(creditorAdditions)
          ? { additions: creditorAdditions }
          : {}),
        ...(paymentItem.isDebitPayment ? { role: 'DEBTOR' } : {}),
      },
      counterpartyAccount: {
        identification: {
          identification:
            creditorScheme === 'ID' || creditorScheme === 'EXTERNAL_ID'
              ? stripSpaces(counterpartyId)
              : stripSpaces(accountNumber),
          schemeName: creditorScheme || 'BBAN',
        },
        ...(accountType ? { accountType } : {}),
        ...(isObjectTruthy(selectedContact) ? { selectedContact } : {}),
        ...(isObjectTruthy(counterpartyAccountAdditions)
          ? { additions: counterpartyAccountAdditions }
          : {}),
      },
      ...(isObjectTruthy(counterpartyBank) ? { counterpartyBank } : {}),
      ...(isObjectTruthy(correspondentBank) ? { correspondentBank } : {}),
      ...(isObjectTruthy(intermediaryBank) ? { intermediaryBank } : {}),
      ...(targetCurrency ? { targetCurrency } : {}),
      ...(mandateIdentifier ? { mandateIdentifier } : {}),
      ...(endToEndIdentification ? { endToEndIdentification } : {}),
      ...(remittanceInformation ? { remittanceInformation } : {}),
      ...(messageToBank ? { messageToBank } : {}),
      ...(paymentReference ? { messageToBank: paymentReference } : {}),
      ...(purposeOfPayment
        ? { purposeOfPayment: { [purposeType]: purposeOfPayment } }
        : {}),
      ...(chargeBearer ? { chargeBearer } : {}),
      ...(isObjectTruthy(transferFeeValue)
        ? { transferFee: { ...transferFeeValue } }
        : {}),
    } as PaymentOrdersValidatePost['transferTransactionInformation'],
    ...(isObjectTruthy(additions) ? { additions } : {}),
  };
};

export const toPostPaymentTemplate = (
  paymentItem: PaymentDetails
): PaymentTemplatesPostRequest => {
  const name = paymentItem[PaymentTemplateFields.templateName];
  const {
    paymentType,
    originatorAccount,
    instructionPriority,
    transferTransactionInformation,
    entryClass,
    additions,
  } = toPostRequest(paymentItem);

  return {
    name,
    paymentType,
    originatorAccount,
    transferTransactionInformation,
    ...(instructionPriority ? { instructionPriority } : {}),
    ...(entryClass ? { entryClass } : {}),
    ...(isObjectTruthy(additions) ? { additions } : {}),
  };
};

const setMainFormDetails = (form: FormGroup, paymentResponse: any) => {
  const initiator = form.get(PaymentBaseFields.initiator);
  const counterparty = form.get(PaymentBaseFields.counterparty);

  const remittanceInfo = form.get(PaymentBaseFields.remittanceInfo);
  const additions = form.get(PaymentBaseFields.additions);
  const selectPaymentOption = form.get(PaymentOptionFields.selectPaymentOption);
  const chargeBearer = form.get(PaymentOptionFields.chargeBearer);
  const defaultAddress = {
    addressLine1: '',
    addressLine2: '',
    streetName: '',
    postCode: '',
    town: '',
    countrySubDivision: '',
    country: '',
  };

  const transferTransactionInformation =
    paymentResponse.transferTransactionInformation || {};
  const counterpartyAccount =
    transferTransactionInformation.counterpartyAccount || {};
  const counterpartyDetails = transferTransactionInformation.counterparty || {};
  const counterPartyIdentification = counterpartyAccount.identification || {};
  const counterpartyAddress = isObjectTruthy(counterpartyDetails.postalAddress)
    ? counterpartyDetails.postalAddress
    : defaultAddress;

  const originatorAccount = paymentResponse.originatorAccount || {};
  const instructedAmount =
    transferTransactionInformation.instructedAmount || {};

  const counterpartyBank =
    transferTransactionInformation.counterpartyBank || {};
  const counterpartyBankDetails = {
    ...counterpartyBank,
    ...(counterpartyBank.name ? { bankName: counterpartyBank.name } : null),
    ...{
      postalAddress: isObjectTruthy(counterpartyBank.postalAddress)
        ? counterpartyBank.postalAddress
        : defaultAddress,
    },
  };

  const alphabetRegex = /^[a-zA-Z]*$/;
  const isIbanTransfer =
    counterPartyIdentification[CounterPartyFields.schemeName] ===
      SchemeNames.IBAN &&
    alphabetRegex.test(
      counterPartyIdentification.identification.substring(0, 2)
    );

  if (!counterpartyBankDetails.postalAddress.country) {
    counterpartyBankDetails.postalAddress.country = isIbanTransfer
      ? counterPartyIdentification.identification.substring(0, 2)
      : form.get(HiddenFields.defaultCountry)?.value;
  }

  const correspondentBank =
    transferTransactionInformation.correspondentBank || {};
  const correspondentBankDetails = {
    ...correspondentBank,
    ...(correspondentBank.name ? { bankName: correspondentBank.name } : null),
    isCorrespondentBankTransfer: isObjectTruthy(correspondentBank),
    ...(correspondentBank.bankBranchCode
      ? { selectAdditionalBankCodeType: 'BRANCH_CODE' }
      : null),
    [CounterPartyFields.enableAdditionalBankDetails]:
      isObjectTruthy(correspondentBank),
  };

  const intermediaryBank =
    transferTransactionInformation.intermediaryBank || {};
  const intermediaryBankDetails = {
    ...intermediaryBank,
    ...(intermediaryBank.name ? { bankName: intermediaryBank.name } : null),
    ...(intermediaryBank.bankBranchCode
      ? { selectAdditionalBankCodeType: 'BRANCH_CODE' }
      : null),
    [CounterPartyFields.enableAdditionalBankDetails]:
      isObjectTruthy(intermediaryBank),
  };

  const counterPartyDetails = {
    [CounterPartyFields.standardEntryClass]: paymentResponse.entryClass,
    [CounterPartyFields.creditorBank]: counterpartyBankDetails,
    [CounterPartyFields.correspondentBank]: correspondentBankDetails,
    [CounterPartyFields.intermediaryBank]: intermediaryBankDetails,
    [CounterPartyFields.schemeName]: counterPartyIdentification.schemeName,
    [CounterPartyFields.recipientId]: counterpartyDetails.recipientId,
    [CounterPartyFields.accountType]: counterpartyAccount.accountType,
    [CounterPartyFields.accountNumber]:
      counterPartyIdentification.identification,
    [CounterPartyFields.name]: counterpartyDetails.name,
    [CounterPartyFields.postalAddress]: counterpartyAddress,
    [CounterPartyFields.messageToBank]:
      transferTransactionInformation.messageToBank || '',
    [CounterPartyFields.targetCurrency]:
      transferTransactionInformation.targetCurrency,
    [CounterPartyFields.remittanceInformation]: transferTransactionInformation
      .remittanceInformation?.content
      ? transferTransactionInformation.remittanceInformation?.content
      : transferTransactionInformation.remittanceInformation || '',
    [CounterPartyFields.endToEndIdentification]:
      transferTransactionInformation.endToEndIdentification,
    [CounterPartyFields.mandateIdentifier]:
      transferTransactionInformation.mandateIdentifier,
    [CounterPartyFields.role]: counterpartyDetails.role,
    [CounterPartyFields.selectedContact]:
      counterpartyAccount.selectedContact || {},
    [CounterPartyFields.id]:
      counterpartyAccount.arrangementId ||
      counterpartyAccount.externalArrangementId,
  };

  const initiatorDetails = {
    [InitiatorFields.id]: originatorAccount.arrangementId,
    [InitiatorFields.name]: originatorAccount.name,
    [InitiatorFields.accountNumber]:
      originatorAccount.identification &&
      originatorAccount.identification.identification,
    currency:
      originatorAccount.currency || paymentResponse.originatorAccountCurrency,
    balance: originatorAccount.balance,
  };

  const decimalIndex = instructedAmount.amount?.indexOf('.');

  const purposeOfPayment: PurposeOfPaymentInfo =
    transferTransactionInformation.purposeOfPayment || {};

  const description = transferTransactionInformation.remittanceInformation
    ?.content
    ? transferTransactionInformation.remittanceInformation?.content
    : transferTransactionInformation.remittanceInformation || '';

  const remittanceInfoDetails = {
    [RemittanceInfoFields.urgent]:
      paymentResponse.instructionPriority === 'HIGH',
    [RemittanceInfoFields.amountCurrencyGroup]: {
      amount: instructedAmount.amount?.slice(0, decimalIndex + 3),
      currency: instructedAmount.currencyCode,
    },
    [RemittanceInfoFields.paymentReference]:
      transferTransactionInformation.messageToBank || '',
    [RemittanceInfoFields.description]:
      paymentResponse.paymentReference || description,
    [RemittanceInfoFields.purposeOfPayment]:
      purposeOfPayment.code || purposeOfPayment.freeText || '',
  };

  const setValues = (control: AbstractControl | null, obj: BaseObject<any>) => {
    if (control) {
      control.patchValue(obj);
    }
  };
  setValues(initiator, initiatorDetails);
  setValues(counterparty, counterPartyDetails);
  setValues(remittanceInfo, remittanceInfoDetails);
  setValues(additions, paymentResponse.additions);
  selectPaymentOption?.patchValue(
    `${paymentResponse.paymentType}::${paymentResponse.instructionPriority}`
  );
  chargeBearer?.patchValue(transferTransactionInformation.chargeBearer);

  return form;
};

export const setFormDetailsFromService = (
  form: FormGroup,
  payment: IdentifiedPaymentOrder
) => {
  const scheduleControl = form.get(PaymentBaseFields.schedule);
  const startDateControl = scheduleControl?.get(ScheduleFields.startDate);
  const isPaymentCopyControl = form.get(HiddenFields.isPaymentCopy);

  if (payment.schedule && !isPaymentCopyControl?.value) {
    const schedule = payment.schedule;
    const freqControl = scheduleControl?.get(ScheduleFields.frequency);
    const endTypeControl = scheduleControl?.get(ScheduleFields.endType);
    const endDateControl = scheduleControl?.get(ScheduleFields.endDate);
    const repeatControl = scheduleControl?.get(ScheduleFields.repeat);

    startDateControl?.patchValue(
      new Date(datePart(schedule.startDate)).toISOString()
    );
    freqControl?.patchValue(schedule.transferFrequency);
    endTypeControl?.patchValue(
      schedule.repeat
        ? endTypes.after
        : schedule.endDate
        ? endTypes.on
        : endTypes.never
    );
    repeatControl?.patchValue(schedule.repeat || 2);
    if (schedule.endDate) {
      startDateControl?.markAsPristine(); // to prevent overwriting endDate with calculated value
      endDateControl?.patchValue(new Date(schedule.endDate).toISOString());
    }
  } else if (!payment.schedule && !isPaymentCopyControl?.value) {
    startDateControl?.patchValue(
      new Date(payment.requestedExecutionDate || '').toISOString()
    );
  }

  return setMainFormDetails(form, payment);
};

export const setFormDetailsFromResponse = (
  form: FormGroup,
  paymentResponse: any,
  { id, name }: Partial<TemplateParams> = {}
) => {
  const paymentTemplateName = form.get(PaymentTemplateFields.templateName);
  const paymentTemplateId = form.get(PaymentTemplateFields.paymentTemplateId);

  paymentTemplateName?.patchValue(name);
  paymentTemplateId?.patchValue(id);

  return setMainFormDetails(form, paymentResponse);
};

export const switchPaymentParties = (
  paymentData: PaymentDetails
): PaymentDetails => {
  const { role } = CounterPartyFields;
  const { currency } = InitiatorFields;
  const { initiator, counterparty } = PaymentBaseFields;
  const { [currency]: currencyValue } = paymentData[initiator];

  const initiatorValue = {
    ...paymentData[counterparty],
    ...(currencyValue ? { [currency]: currencyValue } : { [currency]: '' }),
  };
  const counterPartyValue = {
    ...paymentData[initiator],
    [role]: paymentData[counterparty][role],
  };
  return {
    ...paymentData,
    [initiator]: initiatorValue,
    [counterparty]: counterPartyValue,
  };
};

/**
 * @deprecated
 * Will be removed in widget collection v3.11.0
 */
export const updateFreqFieldOptions = (
  field: PaymentFormFieldConfig,
  isRecurrent: boolean,
  widgetOptions: InitiatePaymentOptions
): PaymentFormFieldConfig => {
  const options = [...field.options.options];
  const { isOneOffToRecurrentAllowed, isRecurrentToOneOffAllowed } =
    widgetOptions;
  if (isRecurrent && !isRecurrentToOneOffAllowed) {
    options.shift();
    field = {
      ...field,
      options: { ...field.options, options, defaultValue: options[0].value },
    };
  } else if (!isRecurrent && !isOneOffToRecurrentAllowed) {
    field = { ...field, options: { ...field.options, options: [options[0]] } };
  }

  return field;
};
