import {
  ChangeDetectorRef,
  Component,
  Directive,
  DoCheck,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { distinct, takeUntil } from 'rxjs/operators';
import { Frequencies } from '../../constants';

export interface ScheduleFrequency {
  value: string;
  every: string;
}

export interface ScheduleItem {
  frequency: ScheduleFrequency;
  startDate: string;
  endDate?: string;
  repeat?: number;
  every?: string;
  endType?: string;
  scheduleInfo?: string;
}

export const endTypes = {
  never: 'never',
  on: 'on',
  after: 'after',
};

const endDate = (frequency: ScheduleFrequency, startDate?: string) => {
  const today = startDate ? new Date(startDate) : new Date();
  switch (frequency.value) {
    case Frequencies.ONCE:
      return undefined;
    case Frequencies.WEEKLY: {
      return new Date(
        today.getFullYear(),
        today.getMonth(),
        today.getDate() + 7
      ).toISOString();
    }
    case Frequencies.BIWEEKLY: {
      return new Date(
        today.getFullYear(),
        today.getMonth(),
        today.getDate() + 14
      ).toISOString();
    }
    case Frequencies.MONTHLY: {
      return new Date(
        today.getFullYear(),
        today.getMonth() + 1,
        today.getDate()
      ).toISOString();
    }
    case Frequencies.QUARTERLY: {
      return new Date(
        today.getFullYear(),
        today.getMonth() + 3,
        today.getDate()
      ).toISOString();
    }
    case Frequencies.YEARLY: {
      return new Date(
        today.getFullYear() + 1,
        today.getMonth(),
        today.getDate()
      ).toISOString();
    }
    case Frequencies.DAILY:
    default: {
      return new Date(
        today.getFullYear(),
        today.getMonth(),
        today.getDate() + 1
      ).toISOString();
    }
  }
};

@Component({
  selector: 'bb-schedule-ui',
  templateUrl: './schedule.component.html',
})
export class ScheduleComponent implements OnInit, DoCheck, OnDestroy {
  readonly hostRef: ScheduleComponent = this;

  /**
   * List of available frequencies
   */
  @Input() frequencies: Array<ScheduleFrequency> | undefined;
  /**
   * Minimum repeat allowed
   */
  @Input() minOccurs: number | undefined;
  /**
   * Maximum repeat allowed
   */
  @Input() maxOccurs: number | undefined;
  /**
   * Form with schedule fields
   */
  @Input() scheduleForm: FormGroup | undefined;
  /**
   * Whether the submit button is pressed
   */
  @Input() formSubmitted: boolean | undefined;

  get endTypes() {
    return endTypes;
  }

  get item() {
    return this.rawItem;
  }

  get isSingleTransfer(): boolean | undefined {
    return (
      this.item &&
      this.item.frequency &&
      (this.item.frequency.value
        ? this.item.frequency.value.toLocaleLowerCase() === 'once'
        : false)
    );
  }

  get today(): NgbDateStruct {
    const today = new Date();
    return {
      year: today.getFullYear(),
      month: today.getMonth() + 1,
      day: today.getDate(),
    };
  }

  private readonly gc$ = new Subject<void>();
  private rawItem: ScheduleItem | undefined;

  constructor(private readonly changeDetectorRef: ChangeDetectorRef) {}

  ngDoCheck() {
    if (this.scheduleForm !== undefined) {
      this.rawItem = this.scheduleForm.getRawValue();
    }
  }

  ngOnInit() {
    if (this.scheduleForm === undefined) {
      throw new Error(
        '`scheduleForm` input is required in `bb-schedule-ui` component'
      );
    }

    if (this.frequencies === undefined) {
      throw new Error(
        '`frequencies` input is required in `bb-schedule-ui` component'
      );
    }

    this.rawItem = this.scheduleForm.getRawValue();

    this.onEndTypeChange();
    this.onFrequencyChange();
    this.onStartDateChange();
  }

  ngOnDestroy() {
    this.gc$.next();
    this.gc$.complete();
  }

  private onEndTypeChange() {
    const endTypeControl =
      this.scheduleForm && this.scheduleForm.get('endType');
    const frequencyControl =
      this.scheduleForm && this.scheduleForm.get('frequency');
    if (endTypeControl) {
      endTypeControl.valueChanges
        .pipe(takeUntil(this.gc$))
        .subscribe((endTypeValue: string) => {
          this.setControlsState(endTypeValue);
          if (frequencyControl) {
            this.setEndDate(frequencyControl.value);
          }
        });
    }
  }

  private setControlsState(endTypeValue: string) {
    if (!this.scheduleForm) {
      return;
    }
    const endDateControl = this.scheduleForm.get('endDate');
    const repeatControl = this.scheduleForm.get('repeat');
    if (endDateControl && repeatControl) {
      if (endDateControl.value) {
        endDateControl.enable();
      } else {
        this.setEndDate({ value: Frequencies.DAILY, every: '1' });
        endDateControl.disable();
      }
      if (repeatControl.value) {
        repeatControl.enable();
      } else {
        repeatControl.disable();
      }
      switch (endTypeValue) {
        case endTypes.after: {
          repeatControl.enable();
          endDateControl.disable();
          break;
        }
        case endTypes.on: {
          endDateControl.enable();
          repeatControl.disable();
          break;
        }
        case endTypes.never:
        default: {
          endDateControl.disable();
          repeatControl.disable();
          break;
        }
      }
    }
  }

  private onStartDateChange() {
    const startDateControl =
      this.scheduleForm && this.scheduleForm.get('startDate');
    const startDate = startDateControl && startDateControl.value;

    if (startDateControl) {
      startDateControl.valueChanges
        .pipe(takeUntil(this.gc$), distinct())
        .subscribe((value) => {
          const frequency = this.item && this.item.frequency;
          if (frequency && startDate !== value) {
            this.setEndDate(frequency);
          }
        });
    }
  }

  private onFrequencyChange() {
    if (!this.frequencies) {
      return;
    }
    const frequencyControl =
      this.scheduleForm && this.scheduleForm.get('frequency');
    if (frequencyControl) {
      if (this.isSingleTransfer) {
        frequencyControl.setValue(this.frequencies[0]);
      }
      frequencyControl.valueChanges
        .pipe(takeUntil(this.gc$))
        .subscribe((freq) => {
          this.setEndDate(freq);
        });
    }
  }

  private setEndDate(frequency: ScheduleFrequency) {
    const endDateControl =
      this.scheduleForm && this.scheduleForm.get('endDate');
    const startDateControl =
      this.scheduleForm && this.scheduleForm.get('startDate');
    const startDate = startDateControl && startDateControl.value;
    const endTypeControl =
      this.scheduleForm && this.scheduleForm.get('endType');
    if (
      endDateControl &&
      endTypeControl &&
      endTypeControl.value === this.endTypes.on
    ) {
      endDateControl.setValue(endDate(frequency, startDate));
    }
    this.changeDetectorRef.detectChanges();
  }

  isInvalidControl(control: FormControl): boolean {
    if (!this.scheduleForm) {
      return false;
    }

    if (!this.formSubmitted) {
      control.markAsUntouched();
    }

    return !!this.formSubmitted && control.invalid;
  }
}

@Directive({
  selector:
    '[bbScheduleCustomizable], [bbPayordInitiatePaymentScheduleCustomizable]',
})
export class ScheduleCustomizableDirective extends BbTemplate<
  ScheduleComponent,
  undefined
> {}
