import {
  Component,
  EventEmitter,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PUBSUB, Pubsub, Unsubscribe } from '@backbase/foundation-ang/web-sdk';
import { NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { BehaviorSubject, Subscription } from 'rxjs';
import { RoutableModalParamName } from '../../models/types';
import { RoutableModalService } from './bb-routable-modal-service';

@Component({
  selector: 'bb-routable-modal',
  template: `
    <bb-modal-ui
      class="bb-modal-container"
      [isOpen]="isModalOpen$ | async"
      [modalOptions]="modalOptions || defaultModalOptions"
      (dismissCallback)="closeModal()"
    >
      <bb-modal-header-ui *ngIf="showHeader">
        <ng-template bbCustomModalHeader>
          <div class="bb-stack bb-stack--density-xs">
            <div class="bb-stack__item" *ngIf="modalTitleIcon">
              <bb-icon-ui
                [name]="modalTitleIcon"
                class="bb-text-disabled bb-modal-container__header-icon"
              >
              </bb-icon-ui>
            </div>

            <div class="bb-stack__item" *ngIf="modalTitle">
              <bb-header-ui
                class="bb-modal-container__header-title"
                [headingType]="modalHeadingType"
                [heading]="modalTitle"
              ></bb-header-ui>
            </div>

            <div
              class="bb-stack__item bb-stack__item--push-right"
              *ngIf="closeButton"
            >
              <button
                bbButton
                class="bb-modal-container__close-btn bb-text-support"
                circle="true"
                buttonSize="sm"
                color="unstyled"
                (click)="closeModal()"
                aria-label="Close modal dialog"
                i18n-aria-label="
                  Close template detail dialog@@modal-container.close-btn"
              >
                <bb-icon-ui color="muted" name="clear"></bb-icon-ui>
              </button>
            </div>
          </div>
        </ng-template>
      </bb-modal-header-ui>
      <bb-modal-body-ui>
        <ng-template bbCustomModalBody>
          <ng-content></ng-content>
        </ng-template>
      </bb-modal-body-ui>
    </bb-modal-ui>
  `,
})
export class RoutableModalComponent implements OnDestroy, OnInit {
  /**
   * Modal Options
   */
  @Input() modalOptions?: NgbModalOptions;
  /**
   * Flag to show close button
   */
  @Input() closeButton = false;
  /**
   * Modal window name (url friendly, without spaces)
   */
  @Input() modalWindowName = '';
  /**
   * Flag to show modal header
   */
  @Input() showHeader = true;
  /**
   * Modal Title
   */
  @Input() modalTitle = '';
  /**
   * Modal Title Icon
   */
  @Input() modalTitleIcon = '';
  /**
   * Modal heading type (h1-h6)
   */
  @Input() modalHeadingType = 'h2';
  /**
   * Event emitted when the modal visibility has changed
   */
  @Output() visibilityChange = new EventEmitter();

  isModalOpen$ = new BehaviorSubject(false);

  readonly defaultModalOptions = {
    backdrop: 'static',
    keyboard: false,
    size: 'lg',
    animation: false,
    scrollable: false,
    windowClass: '',
  };
  private subscription!: Subscription;
  unsubscribeEvent!: Unsubscribe;

  constructor(
    private route: ActivatedRoute,
    private readonly modalService: RoutableModalService,
    @Inject(PUBSUB) private readonly eventBus: Pubsub
  ) {}

  ngOnInit() {
    this.unsubscribeEvent = this.eventBus.subscribe(
      this.modalWindowName,
      () => {
        this.modalService.openModal(this.modalWindowName);
      }
    );

    this.subscription = this.route.queryParams.subscribe((params) => {
      if (
        params[RoutableModalParamName] === this.modalWindowName &&
        !this.isModalOpen$.value
      ) {
        this.openModal();
      } else if (this.isModalOpen$.value) {
        this.closeModal();
      }
    });
  }

  ngOnDestroy() {
    this.subscription?.unsubscribe();
    this.unsubscribeEvent?.();
  }

  openModal() {
    this.isModalOpen$.next(true);
    this.visibilityChange.emit(true);
  }

  closeModal() {
    this.isModalOpen$.next(false);
    this.visibilityChange.emit(false);
    this.modalService.closeModal();
  }
}
