import {
  Component,
  Directive,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { debounceTime, distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { DefaultDebounceTime, SearchMinLength } from '../../constants';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-payments-search',
  templateUrl: 'payments-search.component.html',
})
export class PaymentsSearchComponent implements OnDestroy, OnInit {
  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  readonly gc$ = new Subject();
  readonly debounceSearch$ = new Subject<string>();

  searchTerm = '';

  /**
   * Time delay between each search requests in milliseconds.
   * Default is 300
   */
  @Input() debounceTime = DefaultDebounceTime;
  /**
   * Minimum search length
   * Default is 3
   */
  @Input() searchMinLength = SearchMinLength;
  /**
   * Changes will be triggered on search
   */
  @Output() search = new EventEmitter<string>();

  ngOnInit() {
    this.debounceSearch$
      .pipe(
        takeUntil(this.gc$),
        debounceTime(this.debounceTime),
        distinctUntilChanged()
      )
      .subscribe((term: string) => {
        if (!term || term.length >= this.searchMinLength) {
          this.search.emit(term);
        }
      });
  }

  startSearching(query: string) {
    this.searchTerm = query;
    this.debounceSearch$.next(this.searchTerm);
  }

  clearSearching() {
    this.searchTerm = '';
    this.debounceSearch$.next(this.searchTerm);
  }

  submitSearch() {
    if (this.searchTerm.length >= this.searchMinLength) {
      this.search.emit(this.searchTerm);
    }
  }

  ngOnDestroy() {
    this.gc$.next();
    this.debounceSearch$.complete();
    this.gc$.complete();
  }
}

@Directive({
  selector: '[bbPaymentsSearchCustomizable]',
})
export class PaymentsSearchCustomizableDirective extends BbTemplate<
  PaymentsSearchComponent,
  undefined
> {}
