import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { BehaviorSubject, Observable } from 'rxjs';
import { PaymentState } from '../models';
import { IdentifiedPaymentOrder } from '@backbase/data-ang/payment-order';
import { PaymentDetails } from '../../configs/models/form-items.model';

@Injectable()
export class StateService {
  private readonly formSubject = new BehaviorSubject<FormGroup>(
    new FormGroup({})
  );
  private readonly paymentStateSubject = new BehaviorSubject<PaymentState>(
    PaymentState.INITIAL
  );
  private canApprovePayment = false;
  private readonly paymentTemplateSubject = new BehaviorSubject<
    PaymentTemplate | undefined
  >(undefined);
  private readonly paymentEditSubject = new BehaviorSubject<
    IdentifiedPaymentOrder | undefined
  >(undefined);
  private initialEditFormValueItem?: PaymentDetails;

  public get form$(): Observable<FormGroup> {
    return this.formSubject.asObservable();
  }

  public get formInstance() {
    return this.formSubject.value;
  }

  public get paymentState$(): Observable<PaymentState> {
    return this.paymentStateSubject.asObservable();
  }

  public get currentState(): PaymentState {
    return this.paymentStateSubject.value;
  }

  public get canApprove(): boolean {
    return this.canApprovePayment;
  }

  public set canApprove(canApprovePayment: boolean) {
    this.canApprovePayment = canApprovePayment;
  }

  public get paymentTemplateState$(): Observable<PaymentTemplate | undefined> {
    return this.paymentTemplateSubject.asObservable();
  }

  public get paymentEditState$(): Observable<
    IdentifiedPaymentOrder | undefined
  > {
    return this.paymentEditSubject.asObservable();
  }

  public get initialEditFormValue(): PaymentDetails | undefined {
    return this.initialEditFormValueItem;
  }

  public set initialEditFormValue(formValue: PaymentDetails | undefined) {
    this.initialEditFormValueItem = formValue;
  }

  saveForm(form: FormGroup) {
    this.formSubject.next(form);
  }

  resetForm() {
    this.formSubject.next(new FormGroup({}));
  }

  setPaymentState(paymentState: PaymentState) {
    this.paymentStateSubject.next(paymentState);
  }

  setPaymentTemplate(template: PaymentTemplate | undefined) {
    this.paymentTemplateSubject.next(template);
  }

  setEditPayment(payment: IdentifiedPaymentOrder | undefined) {
    this.paymentEditSubject.next(payment);
  }
}
