import {
  Component,
  ChangeDetectionStrategy,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import { NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'bb-confirmation-dialog',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: 'confirmation-dialog.component.html',
})
export class ConfirmationDialogComponent {
  readonly modalOptions: NgbModalOptions = {
    backdrop: 'static',
    keyboard: false,
  };

  comment = '';

  /**
   * The button color for confirm button
   * Default value is `primary`.
   */
  @Input() confirmButtonColor? = 'primary';

  /**
   * The button color for cancel button
   * Default value is `link`.
   */
  @Input() cancelButtonColor? = 'link';

  /**
   * Indicator for confirmation dialog open state.
   * Default value is `false`.
   */
  @Input() isOpen = false;

  /**
   * Payload data for confirm action
   * Can be any type
   */
  @Input() confirmActionPayload?: { payload: any };

  /**
   * Changes will be triggered on confirmAction event emit.
   */
  @Output() confirm = new EventEmitter();

  /**
   * Changes will be triggered on cancel event emit.
   */
  @Output() cancel = new EventEmitter();

  /**
   * Show the field for the comment
   * Default value is `false`.
   */
  @Input() attachComment = false;

  /**
   * Text for comment field label,
   * Default value is `Add comment`.
   */
  @Input()
  commentFieldLabel = $localize`:@@bb-confirmation-dialog.comment.label:Add comment`;

  private resetComment() {
    if (this.attachComment) {
      this.comment = '';
    }
  }

  onCancelAction() {
    this.cancel.emit();
    this.resetComment();
  }

  onConfirmAction() {
    const _comment = this.comment.trim();
    let emitValue = this.confirmActionPayload?.payload;

    if (this.attachComment && !!_comment) {
      emitValue = {
        payload: this.confirmActionPayload?.payload,
        comment: _comment,
      };
    }

    this.confirm.emit(emitValue);

    this.resetComment();
  }
}
