import {
  Component,
  Input,
  Output,
  EventEmitter,
  forwardRef,
} from '@angular/core';
import {
  FormControl,
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { PurposeOfPayment } from '@backbase/data-ang/payment-order-options';

@Component({
  selector: 'bb-select-purpose-dropdown',
  templateUrl: './select-purpose-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => SelectPurposeDropdownComponent),
    },
  ],
})
export class SelectPurposeDropdownComponent implements ControlValueAccessor {
  _purposes!: Array<PurposeOfPayment>;
  search = new FormControl();
  purposeEntries!: Array<[string, string]>;
  filteredPurposes: Array<PurposeOfPayment> = [];

  /**
   * Description length to show tooltip along
   */
  @Input() longDescription = 150;
  /**
   * Array of country iso codes
   */
  @Input() set purposes(purposes: Array<PurposeOfPayment>) {
    this._purposes = purposes || [];
    this.setupPurposes();
  }
  /**
   * Field options
   */
  @Input() isOptional?: boolean;
  /**
   * Selected purpose
   */
  @Input() selectedPurpose?: string;
  /**
   * Helper text for the component. Defaults to empty string.
   */
  @Input() helperText = '';
  /**
   * Placeholder text. Defaults to empty string.
   */
  @Input() placeholder = '';
  /**
   * Label of the component. Defaults to empty string.
   */
  @Input() label = '';
  /**
   * Placeholder fot search. Defaults to empty string.
   */
  @Input() searchPlaceholder = '';
  /**
   * Icon for the dropdown. Defaults to `toggle-down`
   */
  @Input() dropdownIcon = 'toggle-down';
  /**
   * Emits value when a country is selected
   */
  @Output() selectPurpose = new EventEmitter<string>();

  getSelectedPurposeFull(purposeCode: string): PurposeOfPayment {
    return (
      this._purposes.find(
        (item: PurposeOfPayment) => item?.code === purposeCode
      ) || {
        code: purposeCode,
        description: '',
      }
    );
  }

  setupPurposes() {
    this.purposeEntries = this._purposes.map((purpose: PurposeOfPayment) => [
      purpose.code,
      purpose.description,
    ]);
    this.filteredPurposes = [...this._purposes];
  }

  selectItem(purpose: PurposeOfPayment) {
    this.selectedPurpose = purpose.code;
    this.selectPurpose.emit(purpose.code);
    if (this.search.value) {
      this.search.reset();
    }
  }

  handleSearch(query = '') {
    if (query) {
      this.filteredPurposes = this.purposeEntries.reduce(
        (acc: Array<PurposeOfPayment>, [code, description]) => {
          if (
            code.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1 ||
            description.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) >
              -1
          ) {
            acc.push({ code, description });
          }
          return acc;
        },
        []
      );
    } else {
      this.filteredPurposes = this._purposes;
    }
  }

  onChange() {}
  onTouched() {}
  writeValue(): void {}

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
}
