import { Address } from '../models/types';
import { Bank } from '@backbase/data-ang/payment-order';

export const toInteger = (value: any): number => parseInt(value, 10) || 0;

export const modelItemToBoolean = (value: string | boolean): boolean => {
  if (typeof value === 'boolean') {
    return value;
  }
  return value === 'true';
};

export const isObjectTruthy = <T, K extends keyof T>(obj: T): boolean =>
  obj && Object.keys(obj).some((key) => !!obj[key as K]);

function getKeysWithValue(obj: { [key: string]: any }): string[] {
  return Object.entries(obj).reduce(
    (acc: Array<string>, [addrKey, addrValue]) => {
      if (addrValue) {
        acc.push(addrKey);
      }
      return acc;
    },
    []
  );
}

function shouldRemoveCountryFromBeneficiaryBankAddress(
  address: Bank['postalAddress'] | undefined
): boolean {
  let shouldRemoveCountry = false;
  if (address) {
    const keysWithValue = getKeysWithValue(address);
    shouldRemoveCountry =
      keysWithValue.length === 1 && keysWithValue.indexOf('country') !== -1;
  }
  return shouldRemoveCountry;
}

export function isCounterpartyObjectEquals(o1: any, o2: any): boolean {
  const branchCode1 = o1?.creditorBank?.bankBranchCode;
  const branchCode2 = o2?.creditorBank?.bankBranchCode;

  if (branchCode1 === undefined || branchCode2 === undefined) {
    delete o1?.creditorBank?.bankBranchCode;
    delete o2?.creditorBank?.bankBranchCode;
  }

  // Remove `selectedContact` from comparison (edited payment doesn't have beneficiary's account id)
  const { selectedContact: toExclude1, ...rest1 } = o1;
  const { selectedContact: toExclude2, ...rest2 } = o2;

  o1 = rest1;
  o2 = rest2;

  if (!o1.hideBeneficiaryFields || !o2.hideBeneficiaryFields) {
    const { hideBeneficiaryFields: toExclude3, ...rest3 } = o1;
    const { hideBeneficiaryFields: toExclude4, ...rest4 } = o2;

    o1 = rest3;
    o2 = rest4;
  }

  if (
    shouldRemoveCountryFromBeneficiaryBankAddress(
      o1.creditorBank?.postalAddress
    ) ||
    shouldRemoveCountryFromBeneficiaryBankAddress(
      o2.creditorBank?.postalAddress
    )
  ) {
    const { country: toExclude5, ...rest5 } = o1.creditorBank.postalAddress;
    const { country: toExclude6, ...rest6 } = o2.creditorBank.postalAddress;

    o1.creditorBank.postalAddress = rest5;
    o2.creditorBank.postalAddress = rest6;
  }

  return (o1.id && o1.id === o2.id) || objectEquals(o1, o2);
}

export function objectEquals(o1: object, o2: object) {
  return (
    JSON.stringify(o1, falsyValueReplacer) ===
    JSON.stringify(o2, falsyValueReplacer)
  );
}

function falsyValueReplacer<T>(_: string, val: T): T | '' {
  return val || '';
}

export const isAddressTruthy = (address?: Address) => {
  const {
    addressLine1 = '',
    addressLine2 = '',
    postCode = '',
    town = '',
  } = address || {};
  return isObjectTruthy({ addressLine1, addressLine2, postCode, town });
};

export const filterObjectValues = <T, K extends keyof T>(obj: T) => {
  for (const key in obj) {
    if (!obj[key] || !isObjectTruthy(obj[key])) {
      delete obj[key];
    }
  }
  return obj;
};

export const addressToString = (address: Address) =>
  [
    address[`addressLine1`],
    address[`addressLine2`],
    address[`streetName`],
    address[`postCode`],
    address[`town`],
    address[`countrySubDivision`],
  ]
    .filter(Boolean)
    .join(', ');

export function stripSpaces(str = '') {
  return str?.replace(/\s/g, '');
}

export function toNumber(item: string) {
  return parseFloat(item) || 0;
}

export function parseString(str: string, ...args: any): string {
  let i = 0;
  return str.replace(/%s/g, () => args[i++]);
}
