import {
  Component,
  ChangeDetectionStrategy,
  Input,
  Output,
  EventEmitter,
  OnInit,
} from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable, BehaviorSubject } from 'rxjs';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { DeprecationsService } from '@backbase/foundation-ang/core';
import { tap } from 'rxjs/operators';
import { PaymentTemplatesData } from '../../models/types';

@Component({
  selector: 'bb-payment-template-selector',
  templateUrl: 'payment-template-selector.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentTemplateSelectorComponent implements OnInit {
  /**
   * Items to display in the payment template selector.
   */
  @Input() items!: Observable<PaymentTemplatesData>;

  /**
   * @deprecated since version 3.9.1
   */
  @Input() templatesList?: PaymentTemplate[];

  /**
   * Payment template FormControl.
   */
  @Input() paymentTemplateSearch?: FormControl;

  /**
   * Flag indicates if the list is loading.
   * Default value is `false`.
   */
  @Input() loading = false;

  /**
   * Name of the template selector.
   * Default value is ``
   */
  @Input() templateName = '';

  /**
   * Term value on searching a template
   */
  @Input() set searchValue(term: string) {
    this.onSearchPaymentTemplates(term);
    this.searchModel = term;
  }

  /**
   * Emits the values for the payment form.
   */
  @Output() setValues = new EventEmitter<PaymentTemplate>();

  /**
   * Event emmiter for loading payment templates.
   */
  @Output() loadMoreTemplates = new EventEmitter<number>();

  /**
   * Event emmiter for search payment templates.
   */
  @Output() searchTemplates = new EventEmitter<string>();

  /**
   * Event emmiter for clear the payment form.
   */
  @Output() clear = new EventEmitter<string>();

  searchTerm = new BehaviorSubject<string>('');
  templates$!: Observable<PaymentTemplatesData>;
  searchModel?: string;
  templatesCount!: number;

  loadMore(page: number) {
    this.loadMoreTemplates.emit(page);
  }

  onSetValues(acc: PaymentTemplate) {
    this.setValues.emit(acc);
  }

  onSearchPaymentTemplates(value: string) {
    this.searchTemplates.emit(value);
    this.searchTerm.next(value);
  }

  onClearForm(name: string) {
    this.clear.emit(name);
    this.templateName = name;
  }

  constructor(private deprecationsService: DeprecationsService) {}

  ngOnInit() {
    if (this.templatesList) {
      this.deprecationsService.logDeprecatedFeature(
        `The templatesList property has been deprecated since 3.9.1 and will be removed in 3.10.0.`
      );
    }
    this.items = this.items.pipe(
      tap(
        (templateItems) =>
          (this.templatesCount =
            this.templatesCount ?? templateItems.totalCount)
      )
    );
  }
}
