import {
  ScrollToElementBehavior,
  ScrollToElementBlock,
  ScrollToElementConfigOptions,
  ScrollToElementInline,
} from '../models';

export function scrollToElement(
  elem: HTMLElement,
  selector: string,
  options?: ScrollToElementConfigOptions
) {
  const defaultOptions: ScrollToElementConfigOptions = {
    behavior: ScrollToElementBehavior.SMOOTH,
    block: ScrollToElementBlock.CENTER,
    inline: ScrollToElementInline.NEAREST,
  };

  setTimeout(() => {
    const errorElems = elem?.querySelectorAll(selector);
    if (errorElems?.length) {
      errorElems[0].scrollIntoView(
        Object.assign(defaultOptions, options || {})
      );
    }
  });
}

export function focusChildElement(selector: string, parent?: HTMLElement) {
  setTimeout(() => {
    const child = parent?.querySelector(selector) as HTMLElement;
    child?.focus();
  });
}

export function focusErrorElement(
  parent: HTMLElement,
  selector = 'small.bb-input-validation-message'
) {
  toggleTabIndex(selector, parent);
  scrollToElement(parent, selector);
  focusChildElement(selector, parent);
}

export function toggleTabIndex(
  selector: string,
  parent?: HTMLElement,
  focusable = true
) {
  const child = parent?.querySelector(selector) as HTMLElement;
  child?.setAttribute('tabindex', focusable ? '0' : '-1');
}
