import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { TableDirective } from '@backbase/ui-ang/table';

@Component({
  selector: 'bb-table-checkbox',
  templateUrl: './table-checkbox.component.html',
})
export class TableCheckboxComponent {
  /**
   * The data of the parent row
   */
  @Input() row?: any;
  /**
   * The ID of the parent row
   */
  @Input() rowId?: string;

  @ViewChild('checkbox', { read: ElementRef, static: true })
  checkboxInput?: ElementRef<HTMLInputElement>;

  get rowItem(): any {
    return this.row || this.rowId || undefined;
  }

  get isChecked(): boolean {
    return this.parentTable.isSelected(this.rowItem);
  }

  constructor(private readonly parentTable: TableDirective) {}

  onClick(event: Event) {
    event.stopPropagation();
    if (this.checkboxInput) {
      if (event.target !== this.checkboxInput.nativeElement) {
        this.checkboxInput.nativeElement.checked =
          !this.checkboxInput.nativeElement.checked;
      }
      this.parentTable.toggleItem(
        this.rowItem,
        this.checkboxInput.nativeElement.checked
      );
    }
  }
}
