import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Inject, Injectable, OnDestroy, Optional } from '@angular/core';
import {
  ContactsHttpService as ContactsService,
  ContactsPostRequestBody,
  ContactsPostResponseBody,
} from '@backbase/data-ang/contact-manager';
import {
  ApprovalStatus,
  PaymentOrderGetResponse,
  PaymentOrderProgressStatusGet,
  PaymentOrderPut,
  PaymentOrdersHttpService as PaymentOrdersService,
  PaymentOrdersPost,
  PaymentOrdersPostResponse,
  PaymentOrdersValidatePost,
  PaymentOrdersValidatePostResponse,
  RateGet,
} from '@backbase/data-ang/payment-order';
import {
  PaymentTemplate,
  PaymentTemplatesHttpService as PaymentTemplatesService,
  PaymentTemplatesPostRequest,
  PaymentTemplatesPostResponse,
} from '@backbase/data-ang/payment-template';
import {
  A2aClientHttpService as A2aClientService,
  DetailedExternalAccount,
} from '@backbase/data-ang/payment-order-a2a';
import { ProductSummaryHttpService, ProductSummaryItem } from '@backbase/data-ang/arrangements';
import { BehaviorSubject, EMPTY, Observable, of, Subject, throwError, timer } from 'rxjs';
import {
  catchError,
  concatMap,
  debounce,
  exhaustMap,
  map,
  pluck,
  scan,
  shareReplay,
  switchMap,
  tap,
} from 'rxjs/operators';
import {
  arrangementsPageSize,
  BeneficiaryList,
  contactsPageSize,
  dataItemPermissionParams,
  defaultPrivilege,
  defaultResourceName,
  paymentTemplatesPageSize,
} from '../constants';
import { arrangementItemFromService, contactListFromService } from '../mappers';
import { UsersHttpService } from '@backbase/data-ang/accesscontrol';
import { PaymentErrors } from '../models';
import { BB_PAYORD_OMNI_PAYMENT_CONFIG_TOKEN } from '../../configs/constants';
import { PayordOmniPaymentConfig } from '../../configs/models/form-items.model';
import { DeprecationsService } from '@backbase/foundation-ang/core';
import { Notification, NotificationService } from '@backbase/ui-ang/notification';
import { fromResponse } from '../helpers/http-helpers';
import {
  ArrangementItem,
  ArrangementList,
  ArrangementsContextParameters,
  ContactList,
  ContactsAndArrangementsGetParams,
  PaymentOrderResponseItem,
  PaymentTemplateAction,
  PaymentTemplatesData,
  PaymentTemplatesGetParams,
} from '../models/types';

const initialTemplatesParams: PaymentTemplatesGetParams = {
  from: 0,
  size: paymentTemplatesPageSize,
  intendedAction: PaymentTemplateAction.CREATE,
};

@Injectable()
export class PaymentOrderHttpService implements OnDestroy {
  private readonly paymentResponseSubject: Subject<PaymentOrderResponseItem> = new Subject();
  private readonly contactsGetParams = new BehaviorSubject<ContactsAndArrangementsGetParams>({
    from: 0,
    size: contactsPageSize,
  });
  private readonly creditAccountsParams = new BehaviorSubject<ContactsAndArrangementsGetParams>({
    from: 0,
    creditAccount: true,
    size: arrangementsPageSize,
  });
  private readonly debitAccountsParams$ = new BehaviorSubject<ContactsAndArrangementsGetParams>({
    from: 0,
    debitAccount: true,
    size: arrangementsPageSize,
  });
  private readonly paymentTemplateParams = new BehaviorSubject<PaymentTemplatesGetParams>(initialTemplatesParams);

  private readonly creditAccountsParameters$ = this.creditAccountsParams.pipe(
    map(this.buildArrangementsContextParameters.bind(this)),
  );

  private readonly dataItemParams$ = new Subject<string>();

  resourceName: string | undefined;
  businessFunction!: string;
  arrangementBusFunctions!: string;
  privilege: string | undefined;
  paymentType: string | undefined;
  loading$ = new BehaviorSubject<boolean>(true);

  public readonly dataItemPermissions$ = this.dataItemParams$.pipe(
    switchMap((id) => this.handleDataItemPermissions(id)),
  );

  public readonly arrangementUserPermissions$ = this.dataItemParams$.pipe(
    switchMap((id) => this.getArrangementUserPermission(id)),
  );

  public readonly createPermission$ = this.settings ? this.getUserPermission() : EMPTY;

  public paymentResponse: Observable<PaymentOrderResponseItem | HttpErrorResponse> =
    this.paymentResponseSubject.asObservable();

  public debitAccounts: Observable<ArrangementList> = this.debitAccountsParams$.pipe(
    map(this.buildArrangementsContextParameters.bind(this)),
    exhaustMap((params) => this.fetchArrangementList(params)),
    shareReplay(1),
    scan((acc: ArrangementList, curr: ArrangementList) => {
      const { merge, from } = this.debitAccountsParams$.value;
      return this.mergeArrangementList(acc, curr, (from as number) > 0 && !!merge);
    }),
  );

  public contacts: Observable<ContactList | HttpErrorResponse> = this.contactsGetParams.pipe(
    exhaustMap(({ isLoadMore, ...params }) =>
      this.contactsData.getContacts(params, 'response').pipe(
        map((response) => contactListFromService(response, isLoadMore)),
        catchError((err: HttpErrorResponse) => of(err)),
      ),
    ),
    shareReplay(1),
  );

  public creditAccounts: Observable<ArrangementList | HttpErrorResponse> = this.creditAccountsParameters$.pipe(
    exhaustMap((params) => this.fetchArrangementList(params)),
    shareReplay(1),
    scan((acc: ArrangementList, curr: ArrangementList) => {
      const { merge, from } = this.creditAccountsParams.value;
      return this.mergeArrangementList(acc, curr, (from as number) > 0 && !!merge);
    }),
    catchError((err: HttpErrorResponse) => of(err)),
  );

  public connectedAccounts$: Observable<DetailedExternalAccount[] | undefined> = this.paymentOrdersA2ADataHttpService
    .getAll('response')
    .pipe(
      map(fromResponse),
      map((res) => res.accounts && res.accounts.filter((acc: DetailedExternalAccount) => acc.status === 'Active')),
      shareReplay(1),
    );

  public readonly paymentTemplates$: Observable<PaymentTemplatesData> = this.paymentTemplateParams.pipe(
    tap(() => this.loading$.next(true)),
    debounce((par) => (par.query ? timer(1000) : EMPTY)),
    switchMap((params) => this.loadPaymentTemplates({ ...params, paymentType: this.paymentType })),
    scan((acc: any, curr: any) => this.mergeResults(acc, curr)),
    tap(() => this.loading$.next(false)),
  );

  public readonly templatesList$ = (paymentType: string) =>
    this.paymentTemplateData
      .getPaymentTemplates({ ...initialTemplatesParams, paymentType }, 'response')
      .pipe(map((response: HttpResponse<PaymentTemplate[]>) => response.body));

  private buildArrangementsContextParameters(
    params: ContactsAndArrangementsGetParams,
  ): ArrangementsContextParameters & ContactsAndArrangementsGetParams {
    return {
      resourceName: this.resourceName || defaultResourceName,
      businessFunction: this.arrangementBusFunctions ?? this.businessFunction,
      privilege: this.privilege || defaultPrivilege,
      ...params,
    };
  }

  private fetchArrangementList(params: ArrangementsContextParameters): Observable<ArrangementList> {
    return this.productSummaryData.getArrangementsByBusinessFunction(params, 'response').pipe(
      map(({ headers, body }: HttpResponse<ProductSummaryItem[]>) => {
        const totalCount = headers.get('x-total-count') || '0';
        const items = body || [];
        return {
          count: parseInt(totalCount, 10) || items.length,
          items: items.map(arrangementItemFromService),
        };
      }),
    );
  }

  constructor(
    private readonly paymentOrderData: PaymentOrdersService,
    private readonly paymentTemplateData: PaymentTemplatesService,
    private readonly contactsData: ContactsService,
    private readonly productSummaryData: ProductSummaryHttpService,
    private readonly accessgroupData: UsersHttpService,
    private readonly notificationService: NotificationService,
    @Optional()
    @Inject(BB_PAYORD_OMNI_PAYMENT_CONFIG_TOKEN)
    readonly settings: PayordOmniPaymentConfig,
    readonly paymentOrdersA2ADataHttpService: A2aClientService,
    private readonly deprecationService: DeprecationsService,
  ) {
    this.deprecationService.logDeprecatedFeature(
      '"handleDataItemPermissions" method and "dataItemPermissions$" property are deprecated and will be removed in next major release',
    );
  }

  private getUserPermission() {
    return this.accessgroupData
      .getCheckUserPermission(
        {
          ...this.settings.businessConfig,
          privileges: 'create',
        },
        'response',
      )
      .pipe(shareReplay(1));
  }

  postPaymentOrder(paymentOrder: PaymentOrdersPost): Observable<PaymentOrdersPostResponse> {
    return this.paymentOrderData.postPaymentOrders({ paymentOrdersPost: paymentOrder });
  }

  putPaymentOrder(paymentOrder: PaymentOrderPut, paymentOrderId: string): Observable<PaymentOrdersPostResponse> {
    return this.paymentOrderData.putPaymentOrderById({
      paymentOrderId,
      paymentOrderPut: paymentOrder,
    });
  }

  getPayment(id: string): Observable<PaymentOrderGetResponse> {
    return this.paymentOrderData.getPaymentOrderById({ paymentOrderId: id }, 'response').pipe(map(fromResponse));
  }

  getPaymentProgressStatus(paymentOrderId: string): Observable<PaymentOrderProgressStatusGet | null> {
    return this.paymentOrderData
      .getProgressStatusByPaymentOrderId({ paymentOrderId }, 'response')
      .pipe(map((response: HttpResponse<PaymentOrderProgressStatusGet>) => response.body));
  }

  handleDataItemPermissions(dataItemId: string) {
    return this.accessgroupData
      .getDataItemPermissionsContext({
        resourceName: dataItemPermissionParams.resourceName,
        privilege: dataItemPermissionParams.privilege,
        dataGroupType: dataItemPermissionParams.dataGroupType,
        dataItemId,
      })
      .pipe(
        pluck(0, 'permissions'),
        map((permissions) => permissions?.map((permission) => permission.businessFunction)),
      );
  }

  getArrangementUserPermission(id: string) {
    return this.accessgroupData
      .getArrangementUserPermissionCheck({
        id,
        resource: dataItemPermissionParams.resourceName,
        function: this.businessFunction,
        privilege: dataItemPermissionParams.privilege,
      })
      .pipe(catchError((error) => of(error)));
  }

  loadDataItemPermissions(id: string) {
    this.dataItemParams$.next(id);
  }

  ngOnDestroy(): void {
    this.paymentResponseSubject.complete();
    this.contactsGetParams.complete();
    this.creditAccountsParams.complete();
  }

  loadMoreContacts(page = 0, params?: ContactsAndArrangementsGetParams) {
    this.contactsGetParams.next({
      from: page,
      isLoadMore: true,
      size: contactsPageSize,
      ...params,
    });
  }

  loadMoreCreditAccounts(page = 0, params?: ContactsAndArrangementsGetParams) {
    this.creditAccountsParams.next({
      from: page,
      creditAccount: true,
      ...(params?.query && { searchTerm: params.query }),
      isLoadMore: true,
      size: arrangementsPageSize,
      ...params,
    });
  }

  fetchCreditAccounts(query = '', params?: ContactsAndArrangementsGetParams) {
    this.contactsGetParams.next({
      ...(query && { query }),
      from: 0,
      ...params,
    });
    this.creditAccountsParams.next({
      ...(query && { searchTerm: query }),
      from: 0,
      creditAccount: true,
      ...params,
    });
  }

  loadMoreDebitAccounts(params: ContactsAndArrangementsGetParams): void {
    this.debitAccountsParams$.next({
      ...this.debitAccountsParams$.value,
      ...params,
    });
  }

  validatePayment(payment: PaymentOrdersValidatePost): Observable<PaymentOrdersValidatePostResponse> {
    return this.paymentOrderData
      .postValidate({ paymentOrdersValidatePost: payment }, 'response')
      .pipe(map(fromResponse));
  }

  saveNewContact(contact: ContactsPostRequestBody) {
    return this.contactsData.postContacts({ contactsPostRequestBody: contact }, 'response').pipe(
      map((res: HttpResponse<ContactsPostResponseBody>) => res.body),
      catchError(() => of(null)),
    );
  }

  postPaymentTemplate(requestObject: PaymentTemplatesPostRequest): Observable<PaymentTemplatesPostResponse | null> {
    return this.paymentTemplateData
      .postPaymentTemplates({ paymentTemplatesPostRequest: requestObject })
      .pipe(tap(() => this.loadMore(0)));
  }

  editPaymentTemplate(
    paymentTemplateId: string,
    paymentTemplatesPutRequest: PaymentTemplatesPostRequest,
  ): Observable<PaymentTemplatesPostResponse | null> {
    return this.paymentTemplateData.putPaymentTemplateById({
      paymentTemplateId,
      paymentTemplatesPutRequest,
    });
  }

  getCurrencyExchangeRate(currencyFrom: string, currencyTo: string): Observable<RateGet | null> {
    return this.paymentOrderData.getRate({ currencyFrom, currencyTo }, 'response').pipe(
      map((response) => response && response.body),
      catchError(() => of(null)),
    );
  }

  getAccountByAccountId(id: string, accountNumber: string): Observable<ArrangementItem> {
    return this.productSummaryData
      .getArrangementsByBusinessFunction(
        {
          resourceName: this.resourceName || defaultResourceName,
          businessFunction: this.businessFunction,
          privilege: this.privilege || defaultPrivilege,
          searchTerm: accountNumber,
        },
        'response',
      )
      .pipe(
        map(fromResponse),
        map((items: ProductSummaryItem[]) => items.find((item) => item.id === id)),
        map((item) => {
          if (item) {
            return arrangementItemFromService(item);
          } else {
            throw new Error('No account found...');
          }
        }),
      );
  }

  private loadPaymentTemplates(params: PaymentTemplatesGetParams): Observable<PaymentTemplatesData> {
    return this.paymentTemplateData.getPaymentTemplates(params, 'response').pipe(
      map((response: HttpResponse<PaymentTemplate[]>) => {
        const items = response.body || [];
        return {
          items,
          totalCount: parseInt(response.headers.get('x-total-count') || '0', 10) || items.length,
          params,
        };
      }),
    );
  }

  private mergeResults(acc: PaymentTemplatesData, curr: PaymentTemplatesData): PaymentTemplatesData {
    return {
      totalCount: curr.totalCount || curr.items.length,
      items: curr.params.from === 0 ? curr.items : [...acc.items, ...curr.items],
      params: curr.params,
    };
  }

  private mergeArrangementList(
    accumulator: ArrangementList,
    currentValue: ArrangementList,
    merge: boolean,
  ): ArrangementList {
    return {
      count: currentValue.count || currentValue.items.length,
      items: merge ? [...accumulator.items, ...currentValue.items] : currentValue.items,
    };
  }

  loadMore(page: number) {
    const currentPage = this.paymentTemplateParams.value.from || 0;
    this.paymentTemplateParams.next({
      ...this.paymentTemplateParams.value,
      from: currentPage + page,
    });
  }

  search(query: string) {
    this.paymentTemplateParams.next({
      ...this.paymentTemplateParams.value,
      from: 0,
      ...{ query },
    });
  }

  manageErrorResponse(response: PaymentErrors) {
    const notification = {
      header: response.notificationHeader,
      headerContext: response.errorResponse,
      message: '',
    };

    if (response.errors?.length) {
      response.errors?.forEach((responseError: { key: string; message: string }) => {
        response.paymentErrors?.errorData.find((err) => {
          if (responseError.key === err.key) {
            responseError.message = err.message;
          }
        });
        if (!responseError.message && response.genericMessage) {
          responseError.message = response.genericMessage;
        }

        notification.message = responseError.message;

        this.showNotification(false, notification);
      });
    } else {
      if (response.genericMessage) {
        notification.message = response.genericMessage;
      }
      this.showNotification(false, notification);
    }
  }

  showNotification(success: boolean, notification: Partial<Notification>) {
    this.notificationService.showNotification({
      dismissible: true,
      ttl: 5000,
      modifier: success ? 'success' : 'error',
      header: '',
      message: '',
      ...notification,
    });
  }

  getContacts(params?: ContactsAndArrangementsGetParams): Observable<ContactList | HttpErrorResponse> {
    this.contactsGetParams.next({ ...this.contactsGetParams.value, ...params });
    return this.contacts;
  }

  getCreditAccounts(params?: ContactsAndArrangementsGetParams): Observable<ArrangementList | HttpErrorResponse> {
    this.creditAccountsParams.next({
      ...this.creditAccountsParams.value,
      ...(params?.query && { searchTerm: params.query }),
      ...params,
    });
    return this.creditAccounts;
  }

  getDebitAccounts(params?: ContactsAndArrangementsGetParams): Observable<ArrangementList> {
    this.debitAccountsParams$.next({
      ...this.debitAccountsParams$.value,
      ...params,
    });
    return this.debitAccounts.pipe(shareReplay(1));
  }

  fetchBeneficiaries(params: ContactsAndArrangementsGetParams, type?: BeneficiaryList) {
    const { from, size, query } = params;
    switch (type) {
      case BeneficiaryList.ACCOUNTS:
        this.loadMoreCreditAccounts(from, { size, query });
        break;
      case BeneficiaryList.CONTACTS:
        this.loadMoreContacts(from, { size, query });
        break;
      default:
        this.fetchCreditAccounts(query, { size });
        break;
    }
  }
}
