import { Component, Input, OnInit } from '@angular/core';
import BigNumber from 'bignumber.js';
import {
  PaymentTypeConfig,
  RemittanceDetails,
} from '../../../configs/models/form-items.model';
import { ChargeBearer, ForexDetails } from '../../models';
import { Currency } from '../../models/types';

@Component({
  selector: 'bb-payord-amount-details',
  templateUrl: './amount-details.component.html',
})
export class PayordAmountDetailsComponent implements OnInit {
  /**
   * The config of current payment type
   */
  @Input() config!: PaymentTypeConfig;
  /**
   * Remittance Details
   */
  @Input() details!: RemittanceDetails;
  /**
   * Transfer fee details
   */
  @Input() transferFee?: Currency;
  /**
   * Charge bearer type
   */
  @Input() chargeBearer?: ChargeBearer;
  /**
   * The currency exchange details of the payment
   */
  @Input() forexDetails?: ForexDetails;

  totalAmount!: number | string;
  hasTransferFee = false;
  exchangeRateError = false;

  ngOnInit() {
    const transferFee = this.transferFee || { amount: '0', currencyCode: '' };
    const amount = this.details?.amountGroup?.amount;
    this.hasTransferFee = !!transferFee.amount;
    const calculatedAmount = new BigNumber(
      this.forexDetails?.calculatedAmount || amount || '0'
    )
      .plus(transferFee.amount)
      .toString();
    this.exchangeRateError = this.hasForexError();
    this.totalAmount =
      this.hasTransferFee && this.chargeBearer !== 'BEN'
        ? calculatedAmount
        : amount;
  }

  private hasForexError() {
    return !!(
      this.forexDetails?.fromCurrency !== this.forexDetails?.toCurrency &&
      !this.forexDetails?.rate
    );
  }
}
