export type PaymentHooksCallbackFn = (params?: PaymentHooksParams) => void;
export type FetchPaymentOptionsListenerFn = (
  form: FormGroup,
  fetchOptions: Subject<FormGroup>
) => void;
export type PaymentClearGroupCallbackFn = (
  params?: PaymentClearFormGroupParams
) => void;

export type FormFieldHooksCallbackFn = (params: FormFieldHooksParams) => void;

export interface PaymentClearFormGroupParams {
  formGroup?: FormGroup;
  config?: PaymentFormGroup;
}

export interface ClearOptions {
  label?: string;
  icon?: string;
  onClear: PaymentClearGroupCallbackFn;
}

export interface PaymentHooksParams {
  doneFn: (success?: boolean) => void;
  form: FormGroup;
}

export interface FormFieldHooksParams {
  component: any;
  control: FormControl;
  group: FormGroup;
}

export type PaymentFormFieldHooksOptions = {
  [key in PaymentFormFieldHooks]?: FormFieldHooksCallbackFn;
};

export type PaymentHooksOptions = {
  [key in PaymentHooks]?: PaymentHooksCallbackFn;
};

