import { Subject, combineLatest, Observable } from 'rxjs';
import {
  takeUntil,
  pluck,
  distinctUntilChanged,
  startWith,
  skip,
  filter,
} from 'rxjs/operators';
import {
  AmountOption,
  AmountOptions,
  DependantItem,
  FormFieldHooksParams,
  ProductKinds,
} from '../models/form-items.model';
import {
  CounterPartyFields,
  HiddenFields,
  PaymentBaseFields,
  RemittanceInfoFields,
  ScheduleFields,
} from '../models/form-item-names.model';
import { getEndDate } from '../../ui/helpers/date-helpers';
import { NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { AbstractControl } from '@angular/forms';
import { Frequencies, ScheduleEndType } from '../../ui/constants/constants';
import { InternalAccountFormItem } from '../../ui/models/types';

export function scheduleEndTypeToggleDependants(value: string) {
  switch (value) {
    case ScheduleEndType.ON:
      return [ScheduleFields.endDate];
    case ScheduleEndType.AFTER:
      return [ScheduleFields.repeat];
    default:
      return;
  }
}

export function scheduleFrequencyToggleDependants(value: string) {
  return value !== Frequencies.ONCE ? [ScheduleFields.endType] : undefined;
}

let onDestroyStartDate$: Subject<void>;
let onDestroyEndDate$: Subject<void>;

const getNgbTime = (date: NgbDate) =>
  new Date(date.year, date.month - 1, date.day).getTime();
const oneDay = 24 * 60 * 60 * 1000;

export function scheduleStartDateOnDestroyHook() {
  if (onDestroyStartDate$) {
    onDestroyStartDate$.next();
    onDestroyStartDate$.complete();
  }
  if (onDestroyEndDate$) {
    onDestroyEndDate$.next();
    onDestroyEndDate$.complete();
  }
}

export function scheduleStartDateOnInitHook({
  control,
  group,
  component,
}: FormFieldHooksParams) {
  onDestroyStartDate$ = new Subject();
  if (group) {
    group.valueChanges
      .pipe(
        takeUntil(onDestroyStartDate$),
        pluck(ScheduleFields.frequency),
        distinctUntilChanged()
      )
      .subscribe((value: Frequencies) => {
        if (value === Frequencies.ONCE) {
          component.options.label = 'Execution date';
        } else {
          component.options.label = 'Start on';
        }
        component.options.markDisabled = (date: NgbDate) =>
          getNgbTime(date) < new Date().getTime() - oneDay;
      });
  }
}

export function scheduleEndDateOnInitHook({
  control,
  group,
  component,
}: FormFieldHooksParams) {
  onDestroyEndDate$ = new Subject();
  const startDate = group && group.get(ScheduleFields.startDate);
  const frequency = group && group.get(ScheduleFields.frequency);
  const endType = group && group.get(ScheduleFields.endType);

  if (startDate && !startDate.pristine && frequency && control.pristine) {
    control.patchValue(getEndDate(frequency.value, startDate.value));
  }

  if (startDate && frequency && endType) {
    combineLatest([
      startDate.valueChanges.pipe(
        startWith(startDate.value),
        distinctUntilChanged()
      ),
      frequency.valueChanges.pipe(
        startWith(frequency.value),
        distinctUntilChanged()
      ),
      endType.valueChanges.pipe(
        startWith(endType.value),
        distinctUntilChanged()
      ),
    ])
      .pipe(takeUntil(onDestroyEndDate$), skip(1))
      .subscribe(() => {
        if (control) {
          const endDate = getEndDate(frequency.value, startDate.value);
          control.patchValue(endDate);
          component.options.markDisabled = (date: NgbDate) =>
            getNgbTime(date) < new Date(endDate || '').getTime();
        }
      });
  }
}

export function toggleForAmountOptions(
  _: any,
  control?: AbstractControl,
  amountOptions?: AmountOptions
): DependantItem[] {
  const selectedAccKind =
    control && control.parent ? control.parent.value?.productKindName : null;
  const product =
    control && control.parent
      ? control.parent.value?.accountData?.defaultRecord
      : null;
  const isAmountOptionsZero = amountOptions?.creditCard?.every(
    (option: AmountOption) =>
      option?.amountApiField
        ? Number(product?.[<string>option.amountApiField]) === 0
        : true
  );

  if (
    selectedAccKind === ProductKinds.creditCard ||
    selectedAccKind === ProductKinds.loanAccount
  ) {
    const items = [RemittanceInfoFields.amountOptions];
    if (selectedAccKind === ProductKinds.creditCard && isAmountOptionsZero) {
      items.push(RemittanceInfoFields.amountCurrencyGroup);
    }
    return [
      {
        parent: PaymentBaseFields.remittanceInfo,
        items,
      },
    ];
  }
  return [
    {
      parent: PaymentBaseFields.remittanceInfo,
      items: [
        RemittanceInfoFields.description,
        RemittanceInfoFields.amountCurrencyGroup,
      ],
    },
  ];
}

// Internal transfer onInit-onDestroy hooks
let internalTransferDestroy$: Subject<void>;
export function internalTransferScheduleFreqOnInitHook({
  control,
  group,
  component,
}: FormFieldHooksParams) {
  internalTransferDestroy$ = new Subject();
  const initialOptions = component.options.options;
  const frequencyCtrl = group.parent
    ?.get(PaymentBaseFields.remittanceInfo)
    ?.get('scheduleFrequency');
  const paymentType$ = group.parent?.get('paymentType')?.valueChanges;

  paymentType$
    ?.pipe(takeUntil(internalTransferDestroy$))
    .subscribe((paymentType) => {
      if (paymentType === 'EXTERNAL_A2A') {
        const optionsWithoutDaily = initialOptions.filter(
          (opt: { value: string }) => opt.value !== Frequencies.DAILY
        );
        component.options.options = optionsWithoutDaily;
      } else {
        component.options.options = initialOptions;
      }
      component.toggleDependants();
    });

  frequencyCtrl?.valueChanges
    .pipe(takeUntil(internalTransferDestroy$), startWith(frequencyCtrl.value))
    .subscribe((scheduleFrequency) => {
      const scheduleOptions = scheduleFrequency || initialOptions;
      component.options.options = scheduleOptions;

      const isFreqExistInOptions = scheduleOptions.find(
        ({ value }: { label: string; value: Frequencies }) =>
          value === control.value
      );

      // Check whether previously selected frequency is in options
      if (!isFreqExistInOptions) {
        control.setValue(component.options.defaultValue);
        component.toggleDependants();
      }
    });
}

export function internalTransferOnDestroyHook() {
  if (internalTransferDestroy$) {
    internalTransferDestroy$.next();
    internalTransferDestroy$.complete();
  }
}

export function internalTransferScheduleAlertOnInitHook({
  group,
  component,
}: FormFieldHooksParams) {
  internalTransferDestroy$ = new Subject();
  const { config, options } = component;
  group?.parent
    ?.get(PaymentBaseFields.counterparty)
    ?.valueChanges.pipe(
      filter((account) => {
        const isCreditCardOrLoan = [
          ProductKinds.creditCard,
          ProductKinds.loanAccount,
        ].includes(account?.productKindName);
        config.hidden = !isCreditCardOrLoan;
        return isCreditCardOrLoan;
      }),
      takeUntil(internalTransferDestroy$)
    )
    .subscribe((account) => {
      const dueDate = new Date(
        account?.accountData?.defaultRecord?.minimumPaymentDueDate
      );
      if (dueDate) {
        const fullMonth = dueDate.toLocaleDateString('default', {
          month: 'long',
        });
        const formattedDueDate = `${fullMonth} ${dueDate.getDate()}, ${dueDate.getFullYear()}`;
        const alert =
          new Date().getTime() >= dueDate.getTime()
            ? options.alerts?.overdue
            : options.alerts?.undue;

        if (alert) {
          alert.description = alert.description?.replace(
            '@@date',
            formattedDueDate
          );
        }

        component.options = {
          ...options,
          ...(alert ? alert : {}),
        };
      }
    });
}

export function internalTransAmountOptToggleDependants(
  _: any,
  control?: AbstractControl
) {
  const showFreqSwitch =
    control && control.parent
      ? control.parent.get('showFrequencySwitch')?.value
      : null;
  const items = showFreqSwitch
    ? [ScheduleFields.frequencySwitch]
    : [ScheduleFields.frequency];
  return [
    {
      parent: PaymentBaseFields.schedule,
      items,
    },
  ];
}

export function internalTransScheduleStartDateOnInitHook({
  control,
  group,
  component,
}: FormFieldHooksParams) {
  scheduleStartDateOnInitHook({ control, group, component });
  internalTransferDestroy$ = new Subject();
  const { options } = component;
  const defaultDescription = options.description;
  component.options = { ...options, description: '' }; // No description until an account is selected
  const date$ = control?.valueChanges.pipe(startWith(control.value));
  const account$ = group?.parent
    ?.get(PaymentBaseFields.counterparty)
    ?.valueChanges.pipe(
      startWith(group.parent.get(PaymentBaseFields.counterparty)?.value),
      filter((account) =>
        [ProductKinds.creditCard, ProductKinds.loanAccount].includes(
          account?.productKindName
        )
      )
    ) as Observable<InternalAccountFormItem>;

  combineLatest([account$, date$])
    .pipe(takeUntil(internalTransferDestroy$))
    .subscribe(([account, date]) => {
      const dueDate = new Date(
        account?.accountData?.defaultRecord?.minimumPaymentDueDate
      ).getTime();
      const executeDate = new Date(date).getTime();
      if (dueDate && executeDate) {
        component.options.description =
          executeDate >= dueDate && dueDate >= new Date().getTime()
            ? defaultDescription
            : '';
      }
    });
}

export function intrabankCheckboxOnInitHook({
  control,
  group,
  component,
}: FormFieldHooksParams) {
  internalTransferDestroy$ = new Subject();
  const contactNameCtrl = group.parent?.get(
    `${PaymentBaseFields.counterparty}.${CounterPartyFields.name}`
  );
  const contactNumberCtrl = group.parent?.get(
    `${PaymentBaseFields.counterparty}.${CounterPartyFields.accountNumber}`
  );
  const selectedContact = group.parent?.get(
    `${PaymentBaseFields.counterparty}.${HiddenFields.selectedBeneficiary}`
  );

  combineLatest([
    contactNameCtrl?.valueChanges as Observable<any>,
    contactNumberCtrl?.valueChanges as Observable<any>,
    selectedContact?.valueChanges as Observable<any>,
  ])
    .pipe(takeUntil(internalTransferDestroy$))
    .subscribe(([typedName, typedAccountNumber, { name, accounts }]) => {
      const { accountNumber, email, phoneNumber } = accounts[0];
      const isFormValuesEqualToSavedContact =
        typedName === name &&
        [accountNumber, email, phoneNumber].includes(typedAccountNumber);
      component.options.disabled = isFormValuesEqualToSavedContact;
      if (isFormValuesEqualToSavedContact) {
        control.reset();
      }
    });
}

export function internalTransferEndTypeOnInitHook({
  group,
  component,
}: FormFieldHooksParams) {
  internalTransferDestroy$ = new Subject();
  const initialOptions = component.options.options;
  const initialCssClasses = component.options.cssClasses;
  const paymentType$ = group.parent?.get('paymentType')?.valueChanges;

  paymentType$
    ?.pipe(takeUntil(internalTransferDestroy$))
    .subscribe((paymentType) => {
      if (paymentType === 'EXTERNAL_A2A') {
        component.options.options = initialOptions.filter(
          (opt: { value: string }) => opt.value !== ScheduleEndType.ON
        );
        component.options.cssClasses = initialCssClasses.filter(
          (i: string) => i !== 'bb-schedule__end-type'
        );
      } else {
        component.options.options = initialOptions;
        component.options.cssClasses = initialCssClasses;
      }
    });
}

export function internalTransferRepeatOnInitHook({
  group,
  component,
}: FormFieldHooksParams) {
  internalTransferDestroy$ = new Subject();
  const initialCssClasses = component.options.cssClasses;
  const paymentType$ = group.parent?.get('paymentType')?.valueChanges;

  paymentType$
    ?.pipe(takeUntil(internalTransferDestroy$))
    .subscribe((paymentType) => {
      component.options.cssClasses = [
        ...initialCssClasses,
        ...(paymentType === 'EXTERNAL_A2A'
          ? ['bb-schedule__repeat--two-option']
          : []),
      ];
    });
}
