import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { AbstractControl, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { toggleTabIndex } from '../../helpers/dom-helpers';
import { isAddressTruthy } from '../../helpers/type-helpers';
import { Address, ViewType } from '../../models/types';

@Component({
  selector: 'bb-address-item-details',
  templateUrl: './address-details.component.html',
})
export class AddressDetailsComponent implements OnInit, OnDestroy, OnChanges {
  private static _id = 0;
  readonly id = AddressDetailsComponent._id++;

  /**
   * The parent formGroup
   */
  @Input() addressForm!: FormGroup;

  /**
   * Type of address
   */
  @Input() addressType!: string;

  /**
   * Instance that holds the address details
   */
  @Input() address: Address = {};

  /**
   * Flag that denoted if contact address is copied
   */
  @Input() isContactAddressCopied = false;
  /**
   * List of states in the address group
   */
  @Input() states?: Array<{ label: string; value: string | number }>;
  /**
   * List of countries in the address group
   */
  @Input() countries?: Array<{ label: string; value: string | undefined }>;
  /**
   * Flag to hide country input in the address component
   */
  @Input() hideCountry = false;

  /**
   * Type of address view. Possible values `FORM`| `CARD`. Defaults to `FORM`
   */
  @Input() viewType = ViewType.BUTTON;

  /**
   * Flag to denote if the debit account has only closed payment permissions
   * Default value is `false`
   */
  @Input() isClosedPayment = false;

  /**
   * Event emitted when address is submitted
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() submit = new EventEmitter();
  /**
   * Event emitted when address is reset
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() reset = new EventEmitter();

  /**
   * Event emitted when address is discarded
   */
  @Output() discard = new EventEmitter();

  /**
   * Event emitted when address is deleted
   */
  @Output() delete = new EventEmitter();

  /**
   * Event emitted when address is editted
   */
  @Output() edit = new EventEmitter();

  /**
   * Event emitted when add address action is triggered
   */
  @Output() add = new EventEmitter();
  private valueChangeSubscription?: Subscription;
  private requiredFields = ['addressLine1', 'postCode', 'town'];

  constructor(private readonly elRef: ElementRef) {}

  ngOnInit() {
    if (!this.hideCountry) {
      this.requiredFields.push('country');
    }
    this.markFieldsAsPristine();
    if (!this.isAddressTruthy()) {
      this.viewType = ViewType.BUTTON;
      this.reset.emit();
    }

    this.valueChangeSubscription = this.addressForm.valueChanges.subscribe(
      () => {
        this.address = this.addressForm.value;
        if (this.viewType !== ViewType.FORM) {
          if (this.isAddressTruthy()) {
            this.submit.emit();
          } else if (this.viewType === ViewType.CARD) {
            this.reset.emit();
          }
        }
      }
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    const validator = this.addressForm?.controls['addressLine1']?.validator;
    const currentView = changes['viewType']?.currentValue;

    if (changes['viewType']) {
      if (currentView === ViewType.FORM) {
        if (!validator) {
          this.toggleValidators(true);
        }
      } else {
        if (currentView === ViewType.CARD && !this.isAddressTruthy()) {
          this.reset.emit();
        }
        if (validator) {
          this.toggleValidators(false);
        }
      }
    }
  }

  markFieldsAsPristine() {
    this.requiredFields.forEach((controlName: string) => {
      const control: AbstractControl = this.addressForm.controls[controlName];
      if (control) {
        control.markAsUntouched();
        control.markAsPristine();
      }
    });
  }

  addAddress() {
    if (this.countries?.length === 1) {
      this.addressForm.patchValue({ country: this.countries[0].value });
    }
    if (this.addressForm.touched) {
      // This prevents address form from showing errors before saving an address
      this.addressForm.markAsUntouched();
    }
    this.add.emit();
  }

  editAddress() {
    this.edit.emit();
  }

  discardAddress() {
    this.discard.emit();
  }

  deleteAddress() {
    this.delete.emit();
  }

  submitAddress() {
    if (this.viewType === ViewType.FORM) {
      this.addressForm.markAsDirty();
      this.addressForm.markAllAsTouched();
    }
    this.submit.emit();
  }

  toggleValidators(addValidators: boolean) {
    this.requiredFields.forEach((controlName: string) => {
      const control: AbstractControl = this.addressForm.controls[controlName];
      const validators = addValidators ? [Validators.required] : null;
      if (control) {
        control.setValidators(validators);
        control.updateValueAndValidity();
      }
    });
  }

  selectCountry(country: string) {
    this.addressForm.patchValue({ country });
  }

  isAddressTruthy() {
    return isAddressTruthy(this.addressForm?.value);
  }

  onFocusout() {
    toggleTabIndex(
      '.bb-input-validation-message',
      this.elRef.nativeElement,
      false
    );
  }

  ngOnDestroy() {
    if (this.valueChangeSubscription) {
      this.valueChangeSubscription.unsubscribe();
    }
  }
}
