import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { PaymentTemplate } from '@backbase/data-ang/payment-template';
import { BehaviorSubject, Subject } from 'rxjs';
import { PaymentOrdersData, Template } from '../../models';
import { PaymentConfigService } from '../../../configs/services/payment-config.service';
import {
  canCopyPayment,
  hasCreatePermission,
} from '../../helpers/payment-config-helpers';
import {
  IdentifiedPaymentOrder,
  IdentifiedTransaction,
  PaymentAction,
} from '@backbase/data-ang/payment-order';
import { SortEvent } from '@backbase/ui-ang/table';
import { getRelativeDate } from '../../helpers/date-helpers';
import {
  PaymentModeType,
  PaymentOrder,
  PaymentTemplatesData,
} from '../../models/types';
import { AvailableActions, Preferences } from '../../constants';

@Component({
  selector: 'bb-table-payments-component',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: 'bb-table-payments.component.html',
})
export class PaymentsTableComponent {
  readonly hostRef = this;

  constructor(private readonly paymentConfigService: PaymentConfigService) {}

  /**
   * Items to be displayed
   */
  @Input() items!: PaymentTemplatesData | PaymentOrdersData;

  /**
   * Search term value.
   */
  @Input() searchTerm?: string;

  /**
   * Search query string (if any)
   */
  @Input() clearSelection = new Subject<Set<string>>();

  /**
   * Response error on payment templates list.
   */
  @Input() responseError?: BehaviorSubject<any>;

  /**
   * Flag indicates if the user has delete permission.
   * Default value is `false`.
   */
  @Input() canDelete = false;

  /**
   * Flag indicates if the user has edit permission.
   * Default value is `false`.
   */
  @Input() canEdit = false;

  /**
   * Flag indicates whether called from Manage transfers
   */
  @Input() isManageTransfersMode = false;

  /**
   * The flag to denote if it is a debit mode
   */
  @Input() isDebitMode = false;

  /**
   * Payment mode
   * Default is `SINGLE`.
   */
  @Input() paymentMode: PaymentModeType = 'ALL';

  /**
   * Total count of payment orders.
   */
  @Input() totalCount?: number;

  /**
   * Number of payment orders shown per page.
   */
  @Input() pageSize?: number;

  /**
   * Select template event emitter
   */
  @Output() selectTemplate = new EventEmitter<PaymentTemplate>();

  /**
   * Click template event emitter.
   */
  @Output() templateItemClick = new EventEmitter<PaymentTemplate>();

  /**
   * Changes will be triggered on paymentItemClick event emit.
   */
  @Output() paymentItemClick = new EventEmitter<IdentifiedPaymentOrder>();

  /**
   * Click delete event emitter.
   */
  @Output() triggerDeleteTemplate = new EventEmitter<Template>();

  /**
   * Changes will be triggered on column sorting.
   */
  @Output() sort = new EventEmitter<SortEvent>();

  /**
   * Selection set
   */
  @Output() selectionSet = new EventEmitter<Set<string>>();

  /**
   * Edit template event emitter.
   */
  @Output() editTemplate = new EventEmitter<Template>();

  /**
   * Approval event emitter
   */
  @Output() approvePayment = new EventEmitter<IdentifiedPaymentOrder>();

  /**
   * Rejection event emitter
   */
  @Output() rejectPayment = new EventEmitter<IdentifiedPaymentOrder>();

  /**
   * Cancel event emitter
   */
  @Output() cancelPayment = new EventEmitter<IdentifiedPaymentOrder>();

  /**
   * Edit event emitter
   */
  @Output() editPayment = new EventEmitter<IdentifiedPaymentOrder>();

  /**
   * Copy payment event emitter
   */
  @Output() copyPayment = new EventEmitter<PaymentOrder>();

  private selection: Set<string> = new Set();
  preferences = Preferences;
  mapPaymentType = (item: PaymentTemplate) =>
    this.paymentConfigService.mapPaymentType(item);

  onSelectTemplate(item: PaymentTemplate) {
    this.selectTemplate.emit(item);
  }

  onTemplateClick(template: PaymentTemplate) {
    this.templateItemClick.emit(template);
  }

  onPaymentItemClick(paymentItem?: IdentifiedPaymentOrder) {
    this.paymentItemClick.emit(paymentItem);
  }

  onEditTemplate(template: PaymentTemplate) {
    this.editTemplate.emit({ ...template, query: this.searchTerm });
  }

  trackById(_index: number, item: PaymentTemplate): string {
    return item.id;
  }

  onSort(event: SortEvent) {
    this.sort.emit(event);
  }

  setSelection(selection: Set<string>) {
    this.selection = selection;
    this.selectionSet.emit(this.selection);
  }

  getIds(
    items?: Array<IdentifiedPaymentOrder | PaymentTemplate>
  ): Array<string> | undefined {
    return items?.map((payment) => payment.id);
  }

  openDeleteTemplateModal(item: PaymentTemplate) {
    this.triggerDeleteTemplate.emit({ ...item, query: this.searchTerm });
  }

  formatExecutionDate(date: string): string {
    let result = date;

    if (result === getRelativeDate()) {
      result = 'today';
    } else if (result === getRelativeDate(1)) {
      result = 'tomorrow';
    }

    return result;
  }

  stopPropagation(event: Event) {
    event.stopPropagation();
  }

  isApprovalProhibited(paymentItem?: IdentifiedPaymentOrder): boolean {
    return this.isActionDisabled(
      paymentItem?.actions,
      AvailableActions.APPROVE
    );
  }

  onApprovePayment(payment: IdentifiedPaymentOrder, event: Event) {
    event.stopPropagation();
    if (!this.isApprovalProhibited(payment)) {
      this.approvePayment.emit(payment);
    }
  }

  isRejectionProhibited(paymentItem?: IdentifiedPaymentOrder): boolean {
    return this.isActionDisabled(paymentItem?.actions, AvailableActions.REJECT);
  }

  onRejectPayment(payment: IdentifiedPaymentOrder, event: Event) {
    event.stopPropagation();
    if (!this.isRejectionProhibited(payment)) {
      this.rejectPayment.emit(payment);
    }
  }

  onCancelPayment(payment: IdentifiedPaymentOrder) {
    if (!this.isCancelProhibited(payment)) {
      this.cancelPayment.emit(payment);
    }
  }

  onEditPayment(payment: IdentifiedPaymentOrder) {
    if (!this.isEditProhibited(payment)) {
      this.editPayment.emit(payment);
    }
  }

  onCopyPayment(payment: IdentifiedPaymentOrder) {
    if (this.canCopyPayment(payment)) {
      this.copyPayment.emit({ ...payment, isPaymentCopy: true });
    }
  }

  canCopyPayment(payment: IdentifiedPaymentOrder): boolean {
    return canCopyPayment(payment);
  }

  isEditProhibited(paymentItem?: IdentifiedPaymentOrder): boolean {
    return this.isActionDisabled(paymentItem?.actions, AvailableActions.EDIT);
  }

  isCancelProhibited(paymentItem?: IdentifiedPaymentOrder): boolean {
    return this.isActionDisabled(paymentItem?.actions, AvailableActions.CANCEL);
  }

  transactionInformation(
    payment: IdentifiedPaymentOrder | undefined
  ): IdentifiedTransaction | undefined {
    return payment?.transferTransactionInformation;
  }

  hasCreatePermission(
    template: PaymentTemplate | IdentifiedPaymentOrder
  ): boolean {
    return hasCreatePermission(template.actions);
  }

  private isActionDisabled(
    actions: Array<PaymentAction> | undefined,
    actionType: PaymentAction
  ): boolean {
    return !actions || actions.indexOf(actionType) === -1;
  }
}
