import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import {
  catchError,
  map,
  switchMap,
  take,
  debounce,
  distinctUntilChanged,
} from 'rxjs/operators';
import {
  BatchOrderPutRequest,
  BatchOrderPutResponse,
  BatchOrdersHttpService,
  BatchOrdersPostRequest,
  BatchOrdersPostResponse,
  BatchPaymentsGetResponse,
  BatchPaymentsPostRequest,
  BatchPaymentsPostResponse,
  BatchPaymentsPutRequest,
  BatchPaymentsPutResponse,
  PostBatchOrdersRequestParams,
} from '@backbase/data-ang/payment-batch';
import {
  BatchTemplatesHttpService,
  BatchTemplatesPostResponse,
  PostBatchTemplatesRequestParams,
} from '@backbase/data-ang/payment-batch-template';
import { BehaviorSubject, EMPTY, Observable, throwError, timer } from 'rxjs';
import {
  GetArrangementsByBusinessFunctionRequestParams,
  ProductSummaryHttpService,
  ProductSummaryItem,
} from '@backbase/data-ang/arrangements';
import { InternalAccountFormItem } from '../models/types';

interface ObservableArrangements {
  search$: BehaviorSubject<string>;
  items$: Observable<InternalAccountFormItem[]>;
}

interface AccountsFactoryConfig
  extends GetArrangementsByBusinessFunctionRequestParams {
  debounceTime?: number;
}

@Injectable({
  providedIn: 'root',
})
export class MultiplePaymentService {
  constructor(
    private batchOrderService: BatchOrdersHttpService,
    private batchTemplateService: BatchTemplatesHttpService,
    private productSummaryService: ProductSummaryHttpService
  ) {}

  public submitting = false;

  createFetchAccountsFactory(config: AccountsFactoryConfig) {
    return this._fetchAccountsFactory.bind(this, config);
  }

  saveBatchOrder(
    data: BatchOrdersPostRequest,
    batchOrderID: string,
    dryRun?: boolean
  ): Observable<BatchOrdersPostResponse | BatchOrderPutResponse> {
    this.submitting = true;
    const param: PostBatchOrdersRequestParams = {
      batchOrdersPostRequest: data,
    };
    if (batchOrderID === '') {
      return this.batchOrderService
        .postBatchOrders(param)
        .pipe(catchError(this.catchResponseError));
    } else {
      return this.updateBatchOrder(data, batchOrderID, dryRun);
    }
  }

  updateBatchOrder(
    event: BatchOrderPutRequest,
    batchOrderID: string,
    dryRun?: boolean,
    intendedCreditTransactionsCount?: number,
    intendedCreditAmount?: number
  ): Observable<BatchOrderPutResponse> {
    return this.batchOrderService
      .updateBatchOrderById({
        batchOrderId: batchOrderID,
        dryRun,
        batchOrderPutRequest: event,
        intendedCreditAmount,
        intendedCreditTransactionsCount,
      })
      .pipe(catchError(this.catchResponseError));
  }

  cancelDraftOrder(batchOrderId: string) {
    this.batchOrderService
      .deleteBatchOrderIdByBatchOrderId({ batchOrderId })
      .pipe(take(1))
      .subscribe();
  }

  saveDraftPayment(
    data: BatchPaymentsPostRequest,
    batchOrderID: string
  ): Observable<BatchPaymentsPostResponse | null> {
    return this.batchOrderService
      .postBatchPayments({
        batchOrderId: batchOrderID,
        batchPaymentsPostRequest: data,
      })
      .pipe(catchError(this.catchResponseError));
  }

  updatePayment(
    batchOrderID: string,
    paymentID: string,
    data: BatchPaymentsPutRequest
  ): Observable<BatchPaymentsPutResponse | null> {
    return this.batchOrderService
      .putBatchPaymentById({
        batchOrderId: batchOrderID,
        batchPaymentId: paymentID,
        batchPaymentsPutRequest: data,
      })
      .pipe(catchError(this.catchResponseError));
  }

  deletePayment(batchOrderId: string, batchPaymentId: string): Observable<any> {
    return this.batchOrderService
      .deleteBatchPaymentById({ batchOrderId, batchPaymentId })
      .pipe(catchError(this.catchResponseError));
  }

  saveAsTemplate(
    batchOrderId: string,
    name: string
  ): Promise<BatchTemplatesPostResponse> {
    const parameter: PostBatchTemplatesRequestParams = {
      batchTemplatesPostRequest: { batchOrderId, name },
    };
    return this.batchTemplateService
      .postBatchTemplates(parameter)
      .pipe(catchError(this.catchResponseError))
      .toPromise();
  }

  getPayments(
    batchOrderId: string,
    options = { from: 0, size: 1000 }
  ): Promise<BatchPaymentsGetResponse> {
    return this.batchOrderService
      .getBatchPaymentsById({ batchOrderId, ...options })
      .pipe(catchError(this.catchResponseError))
      .toPromise();
  }

  private catchResponseError(response: HttpErrorResponse) {
    switch (response.status) {
      case 400:
        return throwError(
          response.error || {
            message: 'Unknown error',
            errors: [{ message: 'Unknown error' }],
          }
        );
      case 401:
        return throwError({
          type: 'TIMEOUT',
          message: 'Session Timeout',
          subMessage: 'Please login again.',
        });
      default:
        return throwError({
          type: 'GENERIC',
          message: 'Unknown Error',
          subMessage: 'Try to reload the page or contact system administrator.',
        });
    }
  }

  private _fetchAccountsFactory(
    config: AccountsFactoryConfig
  ): ObservableArrangements {
    const search$ = new BehaviorSubject<string>('');

    const items$ = search$.pipe(
      distinctUntilChanged(),
      debounce((searchTerm: string) =>
        searchTerm ? timer(config.debounceTime || 0) : EMPTY
      ),
      switchMap((searchTerm: string) => {
        const params = { ...config, ...(searchTerm ? { searchTerm } : {}) };
        return this.productSummaryService
          .getArrangementsByBusinessFunction(params)
          .pipe(
            map((arrangements: ProductSummaryItem[]) =>
              arrangements.map((arrangement) => ({
                id: arrangement.id,
                name: arrangement.name || '',
                alias:
                  (arrangement.userPreferences || {}).alias ||
                  arrangement.bankAlias,
                balance:
                  arrangement.availableBalance || arrangement.bookedBalance,
                bookedBalance: arrangement.bookedBalance,
                availableBalance: arrangement.availableBalance,
                creditLimit: arrangement.creditLimit,
                currency: arrangement.currency || '',
                IBAN: arrangement.IBAN || arrangement.number,
                BBAN: arrangement.BBAN,
                accountNumber: arrangement.BBAN || arrangement.IBAN || '',

                number: arrangement.BBAN || '',
                bankBranchCode: arrangement.bankBranchCode,
                externalTransferAllowed: arrangement.externalTransferAllowed,
                urgentTransferAllowed: arrangement.urgentTransferAllowed,
                additions: arrangement.additions,
                productKindName: arrangement.productKindName,
                originalArrangement: arrangement,
              }))
            )
          );
      })
    );

    return {
      search$,
      items$,
    };
  }
}
