import {
  Input,
  Component,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
} from '@angular/core';
import { BreachInfo } from '@backbase/data-ang/payment-order';

import { PaymentBaseFields } from '../../../configs/models/form-item-names.model';
import {
  CounterPartyDetails,
  InitiatorDetails,
  PaymentDetails,
} from '../../../configs/models/form-items.model';
import {
  BreachedLimitType,
  BreachType,
  Period,
} from '../../constants/constants';
import { BreachReportError } from '../../models/types';

interface PaymentDetailsExtend extends PaymentDetails {
  [PaymentBaseFields.initiator]: InitiatorDetails & { IBAN: string };
  [PaymentBaseFields.counterparty]: CounterPartyDetails & { IBAN: string };
}

@Component({
  selector: 'bb-breach-limit-details',
  templateUrl: 'breach-limit-details.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BreachLimitDetailsModalComponent {
  /**
   * Payment details
   */
  @Input() paymentDetails!: PaymentDetailsExtend;

  /**
   * The breach limit report from the payment order that originated it.
   */
  @Input() error?: BreachReportError;

  /**
   * The flag that controls visibility of the breach limit report.
   * By default is `false`.
   */
  @Input() isOpen = false;

  /**
   * Event emitted when user clicks on close icon.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close = new EventEmitter();

  get breachReportCurrency() {
    return this.error?.breachReport?.[0]?.currency;
  }

  private normalizeBreachReportPeriod(
    breachReportInfo: BreachInfo
  ): BreachInfo {
    return breachReportInfo.timeframe
      ? {
          ...breachReportInfo,
          timeframe: {
            ...breachReportInfo.timeframe,
            period: breachReportInfo.timeframe.period.toUpperCase(),
          },
        }
      : breachReportInfo;
  }

  private flattenBreachReports(): Array<BreachInfo> {
    if (!this.error || !this.error.breachReport) {
      return [];
    }
    return this.error.breachReport
      .map((report) => report.breachInfo)
      .reduce(
        (prev, current) =>
          prev.concat(...current.map(this.normalizeBreachReportPeriod)),
        []
      );
  }

  private compareByPeriod(left: BreachInfo, right: BreachInfo): number {
    const leftPeriod = left.timeframe?.period;
    const rightPeriod = right.timeframe?.period;
    return (
      (<any>Period)[<string>leftPeriod] - (<any>Period)[<string>rightPeriod]
    );
  }

  get sortedBreaches(): Array<BreachInfo> {
    const limits = this.flattenBreachReports();
    const transactional = limits.filter((limit, index) => {
      const isTransactional =
        limit.breachedLimitType === BreachedLimitType.TRANSACTIONAL;
      const hasPeriodicThreshold =
        limit.breachType === BreachType.THRESHOLD &&
        limit.breachedLimitType === BreachedLimitType.PERIODIC;

      if (isTransactional || hasPeriodicThreshold) {
        limits.splice(index, 1);
      }

      return isTransactional;
    });

    return [...transactional, ...limits.sort(this.compareByPeriod)];
  }

  onClose() {
    this.close.emit();
  }

  print() {
    window.print();
  }
}
