import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import {
  ControlValueAccessor,
  FormControl,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { CurrenciesMap, CurrencyToCountryMap } from '../../constants';
import { CurrencySelectorConfig } from '../../models';

@Component({
  selector: 'bb-currency-selector',
  templateUrl: './currency-selector.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => CurrencySelectorComponent),
    },
  ],
})
export class CurrencySelectorComponent implements ControlValueAccessor, OnInit {
  isDisabled = false;
  _currencies!: Array<string>;
  search = new FormControl();
  currencyEntries!: Array<[string, string]>;
  filteredCurrencies: Array<string> = [];
  currencyToCountryMap = CurrencyToCountryMap;
  currenciesMap = CurrenciesMap;
  selectedCurrency?: string;
  value?: string;
  change = new EventEmitter();
  showFlags?: boolean;
  showName?: boolean;
  isSearchable?: boolean;
  dividerIndex?: number;
  searchPlaceholder?: string;
  dropdownIcon?: string;

  /**
   * List of currencies setter
   */
  @Input() set currencies(currencies: Array<string>) {
    this._currencies = currencies || [];
    this.setupCurrencies();
  }

  /**
   * Flag to determine component config
   */
  @Input() config: CurrencySelectorConfig = {};

  /**
   * Input that can be used to specify the `aria-labelledby` attribute.
   */
  @Input('aria-labelledby') ariaLabelledby: string | undefined;

  /**
   * Selected currency output
   */
  @Output() selectCurrency = new EventEmitter<string>();

  setupCurrencies() {
    this.currencyEntries = this._currencies.map((currency: string) => [
      currency,
      this.currenciesMap[currency] ? this.currenciesMap[currency].name : '',
    ]);
    this.filteredCurrencies = [...this._currencies];
    this.selectedCurrency = this.selectedCurrency || this._currencies[0];
    if (this._currencies.length < 2) {
      this.isDisabled = true;
    }
  }

  selectItem(currency: string) {
    this.selectedCurrency = currency;
    this.selectCurrency.emit(currency);
    this.onChange(currency);
    this.value = currency;
  }

  handleSearch(query = '') {
    if (query) {
      this.filteredCurrencies = this.currencyEntries.reduce(
        (acc: Array<string>, [currency, currName]) => {
          if (
            currency.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) >
              -1 ||
            currName.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1
          ) {
            acc.push(currency);
          }
          return acc;
        },
        []
      );
    } else {
      this.filteredCurrencies = this._currencies;
    }
  }

  onChange(_value: string) {}

  onTouched() {}

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  writeValue(value: string): void {
    if (value) {
      this.selectedCurrency = value;
    }
    this.value = value;
  }

  ngOnInit(): void {
    this.showFlags = this.config.showFlags || false;
    this.showName = this.config.showName || false;
    this.isSearchable = this.config.isSearchable || false;
    this.dividerIndex = this.config.dividerIndex;
    this.searchPlaceholder = this.config.searchPlaceholder || '';
    this.dropdownIcon = this.config.dropdownIcon || 'toggle-down';
  }
}
