import {
  Component,
  ChangeDetectionStrategy,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ModalState } from '../../models';
import { BehaviorSubject } from 'rxjs';
import { NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'bb-payment-template-form',
  templateUrl: './create-payment-template-modal.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentTemplateComponent {
  readonly modalOptions: NgbModalOptions = {
    backdrop: 'static',
    keyboard: false,
  };

  /**
   * Modal state flag.
   */
  @Input() modalState!: BehaviorSubject<ModalState>;

  /**
   * Payment template form.
   */
  @Input() paymentTemplateForm!: FormGroup;

  /**
   * Saving template data flag.
   * Default value is false.
   */
  @Input() isSavingTemplate = false;

  /**
   * Close payment template modal event emitter
   */
  @Output() closeModal = new EventEmitter<void>();

  /**
   * Open payment template modal event emitter
   */
  @Output() openModal = new EventEmitter<void>();

  /**
   * Submit form event emitter
   */
  @Output() submitForm = new EventEmitter<void>();

  readonly states = ModalState;
  isSubmitted = false;

  onOpen() {
    this.openModal.emit();
  }

  onClose() {
    this.closeModal.emit();
    this.isSubmitted = false;
  }

  onSubmit() {
    this.submitForm.emit();
    this.isSubmitted = true;
  }
}
