import { HttpResponse } from '@angular/common/http';
import {
  Inject,
  Injectable,
  OnDestroy,
  Optional,
  Provider,
} from '@angular/core';
import {
  BulkPaymentOrdersApprovalPutResponse,
  IdentifiedPaymentOrder,
  PaymentMode,
  PaymentOrderCancelPostResponse,
  PaymentOrderGetResponse,
  PaymentOrdersPostResponse,
  PaymentOrdersValidatePost,
  PaymentOrdersValidatePostResponse,
  RateGet,
} from '@backbase/data-ang/payment-order';
import { ItemModel } from '@backbase/foundation-ang/core';
import {
  BehaviorSubject,
  merge,
  Observable,
  of,
  ReplaySubject,
  Subject,
} from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  finalize,
  map,
  mapTo,
  scan,
  shareReplay,
  switchMap,
  takeUntil,
  tap,
  withLatestFrom,
} from 'rxjs/operators';
import { datePart } from '../helpers/date-helpers';
import { PaymentActionsTypes } from '../models/payment-actions-types';
import {
  FlatPaymentsControlsPayloadModel,
  PaymentsControlsModel,
} from '../models/payments-controls.model';
import {
  LoadPaymentsParams,
  PaymentsResponse,
  PaymentActionsState,
} from '../models/types';
import { PaymentOrdersHttpService } from './payment-orders-http.service';
import { DEFAULT_PAYMENT_PARAMS } from './tokens';

@Injectable()
export class PaymentService implements OnDestroy {
  private readonly destroy$ = new Subject();
  private readonly selectedAccount$ = new Subject<
    LoadPaymentsParams['query']
  >();
  private readonly search$ = new Subject<
    Partial<FlatPaymentsControlsPayloadModel>
  >();
  private readonly changePage$ = new Subject<LoadPaymentsParams['from']>();
  private readonly loadMore$ = new Subject<LoadPaymentsParams['from']>();
  private readonly loadPayments$ = new ReplaySubject<
    LoadPaymentsParams['paymentMode']
  >(1);
  private readonly refreshPayments$ = new Subject();
  private readonly sortPayments$ = new Subject<{
    orderBy?: LoadPaymentsParams['orderBy'];
    direction?: LoadPaymentsParams['direction'];
  }>();
  private readonly loadingSub$ = new ReplaySubject<boolean>(1);
  private readonly actingSub$ = new ReplaySubject<boolean>(1);
  private readonly pageSizeIM$ =
    this.model?.property<number>('pageSize') || of(null);
  readonly loading$ = this.loadingSub$.pipe(distinctUntilChanged());
  readonly acting$ = this.actingSub$.pipe(distinctUntilChanged());
  private pageSizeSub$ = new BehaviorSubject<number | undefined>(undefined);
  private debitMode$ = new BehaviorSubject<boolean | undefined>(undefined);
  private pageSize$ = this.pageSizeSub$.asObservable().pipe(
    withLatestFrom(this.pageSizeIM$),
    map(([pageSize, pageSizeIM]) =>
      //TODO: remove it when migration on journeys for payments will be done
      pageSize === undefined
        ? pageSizeIM || this.defaultPaymentsParams.size
        : pageSize
    )
  );

  private loadingCalls = 0;
  private actingCalls = 0;

  private set loading(val: boolean) {
    val ? this.loadingCalls++ : this.loadingCalls--;
    this.loadingSub$.next(this.loadingCalls > 0);
  }

  private set acting(val: boolean) {
    val ? this.actingCalls++ : this.actingCalls--;
    this.actingSub$.next(this.actingCalls > 0);
  }

  private readonly paymentsActions$ = merge(
    this.loadPayments$.pipe(
      map((paymentMode) => ({
        type: PaymentActionsTypes.LoadPayments,
        paymentMode,
      }))
    ),
    this.refreshPayments$.pipe(
      mapTo({ type: PaymentActionsTypes.RefreshPayments })
    ),
    this.changePage$.pipe(
      map((from) => ({ type: PaymentActionsTypes.ChangePage, from }))
    ),
    this.loadMore$.pipe(
      map((from) => ({ type: PaymentActionsTypes.LoadMore, from }))
    ),
    this.selectedAccount$.pipe(
      map((query) => ({ type: PaymentActionsTypes.SelectAccount, query }))
    ),
    this.sortPayments$.pipe(
      map((sortParams) => ({
        type: PaymentActionsTypes.SelectAccount,
        ...sortParams,
      }))
    ),
    this.search$.pipe(
      map((params) => ({ type: PaymentActionsTypes.Search, ...params }))
    )
  );

  paymentState$ = this.paymentsActions$.pipe(
    withLatestFrom(this.pageSize$, this.debitMode$),
    map(
      ([state, pageSize, isDebitMode]: [
        any,
        number | undefined,
        boolean | undefined
      ]) => ({
        ...state,
        pageSize,
        isDebitMode,
      })
    ), //TODO: remove it when migration on journeys for payments will be done
    scan((state: any, action: any) => ({ ...state, ...action })),
    map((state) => ({ ...this.defaultPaymentsParams, ...(state as object) })),
    map(({ pageSize, isDebitMode, ...state }: PaymentActionsState) => {
      const isRefresh = state.type === 'RefreshPayments';
      const size = pageSize || 50;
      const from = state.from || 0;
      return {
        ...state,
        size: isRefresh ? size * (from + 1) : size,
        from: isRefresh ? 0 : from,
        ...(isDebitMode ? { paymentTypeGroup: 'Debit' } : null),
      };
    }),
    filter((action) => action.type !== undefined),
    shareReplay(1)
  );

  private readonly paymentsEffect$ = this.paymentState$.pipe(
    switchMap(({ type, ...params }) => {
      const isActing = type === PaymentActionsTypes.SelectAccount;
      const isLoadMore = type === PaymentActionsTypes.LoadMore;

      isActing ? (this.acting = true) : (this.loading = true);

      return this.paymentOrdersService.loadPayments(params).pipe(
        map((response) => ({ ...response, isLoadMore })),
        finalize(() =>
          isActing ? (this.acting = false) : (this.loading = false)
        )
      );
    }),
    takeUntil(this.destroy$),
    shareReplay(1)
  );

  private readonly paymentsStateSource$ = new BehaviorSubject<
    { totalCount: number; items: IdentifiedPaymentOrder[] } | undefined
  >(undefined);

  public get payments$() {
    return this.paymentsStateSource$.asObservable();
  }

  constructor(
    private readonly paymentOrdersService: PaymentOrdersHttpService,
    @Optional() private readonly model: ItemModel,
    @Inject(DEFAULT_PAYMENT_PARAMS)
    private readonly defaultPaymentsParams: LoadPaymentsParams
  ) {
    this.paymentsEffect$.subscribe(
      (payload) => this.handlePaymentSuccess(payload),
      (error) => this.paymentsStateSource$.error(error)
    );
  }

  private handlePaymentSuccess(
    payload: PaymentsResponse & { isLoadMore: boolean }
  ) {
    const orders: IdentifiedPaymentOrder[] = [];
    if (payload.isLoadMore) {
      orders.push(...this.handleLoadMore());
    }

    this.paymentsStateSource$.next({
      items: [...orders, ...payload.items],
      totalCount: payload.totalCount || payload.items.length,
    });
  }

  private handleLoadMore() {
    const existingPayments = this.paymentsStateSource$.getValue();
    return existingPayments ? existingPayments.items : [];
  }

  loadPayments(paymentMode?: PaymentMode) {
    this.loadPayments$.next(paymentMode);
  }

  refreshPayments() {
    this.refreshPayments$.next();
  }

  loadMore(from: number) {
    this.loadMore$.next(from);
  }

  changePage(from: number) {
    this.changePage$.next(from);
  }

  sortPayments(
    orderBy: LoadPaymentsParams['orderBy'],
    direction: LoadPaymentsParams['direction']
  ) {
    this.sortPayments$.next({ orderBy, direction });
  }

  setPageSize(value: number) {
    this.pageSizeSub$.next(value);
  }
  setDebitMode(value: boolean) {
    this.debitMode$.next(value);
  }

  cancelPayment(
    paymentOrderId: string,
    version: number
  ): Observable<HttpResponse<PaymentOrderCancelPostResponse>> {
    this.acting = true;
    return this.paymentOrdersService
      .cancelPayment(paymentOrderId, version)
      .pipe(
        finalize(() => (this.acting = false)),
        tap(() => {
          this.handleCancelPaymentSuccess(paymentOrderId);
        })
      );
  }

  private handleCancelPaymentSuccess(paymentOrderId: string) {
    const payments = this.paymentsStateSource$.getValue();
    if (payments) {
      const newItems = payments.items.filter(
        (item) => item.id !== paymentOrderId
      );
      this.paymentsStateSource$.next({
        items: newItems,
        totalCount: payments.totalCount,
      });
    }
  }

  getCurrencyExchangeRate(
    currencyFrom = 'EUR',
    currencyTo = 'USD'
  ): Observable<HttpResponse<RateGet>> {
    return this.paymentOrdersService.getCurrencyExchangeRate(
      currencyFrom,
      currencyTo
    );
  }

  getPaymentOrders(
    params: object
  ): Observable<HttpResponse<IdentifiedPaymentOrder[]>> {
    return this.paymentOrdersService.getPaymentOrders(params);
  }

  getPaymentDetails(
    paymentId: string,
    params: object
  ): Observable<PaymentOrderGetResponse> {
    return this.paymentOrdersService.getPayment(paymentId, params);
  }

  approvePayment(
    paymentId: string,
    comment?: string
  ): Observable<PaymentOrdersPostResponse> {
    return this.paymentOrdersService.approvePayment(paymentId, comment);
  }

  bulkApprovePayments(
    paymentIds: Array<string>,
    comment?: string
  ): Observable<BulkPaymentOrdersApprovalPutResponse | undefined> {
    return this.paymentOrdersService.bulkApprovePayments(paymentIds, comment);
  }

  rejectPayment(
    paymentId: string,
    comment?: string
  ): Observable<PaymentOrdersPostResponse> {
    return this.paymentOrdersService.rejectPayment(paymentId, comment);
  }

  bulkRejectPayments(
    paymentIds: Array<string>,
    comment?: string
  ): Observable<BulkPaymentOrdersApprovalPutResponse | undefined> {
    return this.paymentOrdersService.bulkRejectPayments(paymentIds, comment);
  }

  searchPayments(query: string) {
    this.search$.next({ query, from: 0 });
  }

  applyFilter({ amount, executionDate, ...rest }: PaymentsControlsModel): void {
    const retVal: Partial<FlatPaymentsControlsPayloadModel> = {
      ...rest,
      from: 0,
    };

    if (!retVal.status) {
      retVal.status = [];
    }

    retVal.amountFrom = amount.from || '';
    retVal.amountTo = amount.to || '';
    retVal.executionDateFrom =
      executionDate && executionDate.from ? datePart(executionDate.from) : '';
    retVal.executionDateTo =
      executionDate && executionDate.to ? datePart(executionDate.to) : '';

    this.search$.next(retVal);
  }

  postCancelPayment(
    paymentOrderId: string,
    version: number
  ): Observable<HttpResponse<PaymentOrderCancelPostResponse>> {
    this.acting = true;
    return this.paymentOrdersService
      .postPaymentCancel(paymentOrderId, version)
      .pipe(
        finalize(() => (this.acting = false)),
        tap(() => {
          this.handleCancelPaymentSuccess(paymentOrderId);
        })
      );
  }

  validatePayment(
    payment: PaymentOrdersValidatePost
  ): Observable<PaymentOrdersValidatePostResponse> {
    return this.paymentOrdersService.validatePayment(payment);
  }

  ngOnDestroy() {
    // clean up our subjects
    this.destroy$.complete();
    this.loadMore$.complete();
    this.changePage$.complete();
    this.selectedAccount$.complete();
    this.search$.complete();
    this.loadPayments$.complete();
    this.refreshPayments$.complete();
  }
}

export const providePaymentServices: Provider[] = [
  { provide: PaymentOrdersHttpService, useClass: PaymentOrdersHttpService },
  {
    provide: PaymentService,
    useClass: PaymentService,
    deps: [PaymentOrdersHttpService, ItemModel, DEFAULT_PAYMENT_PARAMS],
  },
];
