import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { map, shareReplay, tap } from 'rxjs/operators';
import {
  PaymentOption,
  PaymentOptionsHttpService as PaymentOrderOptionsDataHttpService,
  PaymentOptionsRequest,
  PaymentOptionsResponse,
  SanctionedCountriesHttpService as SanctionedCountriesServiceHttpService,
  SanctionedCountriesResponse,
  SanctionedCountry,
  SanctionedCurrenciesHttpService as SanctionedCurrenciesServiceHttpService,
  SanctionedCurrenciesResponse,
} from '@backbase/data-ang/payment-order-options';

@Injectable()
export class PaymentOptionsService {
  paymentOptionsSubject$ = new BehaviorSubject<
    Array<PaymentOption> | undefined
  >(undefined);

  constructor(
    private readonly paymentOptionsData: PaymentOrderOptionsDataHttpService,
    private readonly sanctionedCountriesService: SanctionedCountriesServiceHttpService,
    private readonly sanctionedCurrenciesService: SanctionedCurrenciesServiceHttpService
  ) {}

  get paymentOptions$() {
    return this.paymentOptionsSubject$.asObservable();
  }

  fetchPaymentOptions(
    payment: PaymentOptionsRequest
  ): Observable<PaymentOptionsResponse> {
    return this.paymentOptionsData
      .postPaymentOptions({ paymentOptionsRequest: payment })
      .pipe(
        tap((response: PaymentOptionsResponse) => {
          this.paymentOptionsSubject$.next(response.options || []);
        })
      );
  }

  getPaymentOptionsRecordByPaymentType(
    paymentType: string
  ): Observable<PaymentOption[]> {
    return this.paymentOptionsData
      .getPaymentOptionsByPaymentType({ paymentType })
      .pipe(
        map((response: PaymentOption) => (response && [response]) || []),
        shareReplay(1)
      );
  }

  getSanctionedCurrencies(
    countryIso?: string
  ): Observable<SanctionedCurrenciesResponse> {
    return this.sanctionedCurrenciesService
      .getSanctionedCurrencies({ countryIso })
      .pipe(map((response: SanctionedCurrenciesResponse) => response || {}));
  }

  getSanctionedCountries(
    countryIso?: string
  ): Observable<Array<SanctionedCountry>> {
    return this.sanctionedCountriesService
      .getSanctionedCountries({ countryIso })
      .pipe(
        map(
          (response: SanctionedCountriesResponse) => response?.countries || []
        )
      );
  }

  resetPaymentOptions() {
    this.paymentOptionsSubject$.next([]);
  }
}
