import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {
  PaymentOrderCancelPostResponse,
  PaymentOrdersHttpService as PaymentOrdersService,
  IdentifiedPaymentOrder,
  RateGet,
  PaymentOrdersValidatePost,
  BulkPaymentOrdersApprovalPutResponse,
  PaymentOrdersPostResponse,
  PaymentOrdersValidatePostResponse,
  RecordStatus,
  GetPaymentOrdersRequestParams,
  PaymentOrderGetResponse,
} from '@backbase/data-ang/payment-order';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { LoadPaymentsParams } from '../models/types';
import { Actions } from '../constants/constants';
import { fromResponse } from '../helpers/http-helpers';

@Injectable()
export class PaymentOrdersHttpService {
  constructor(private readonly paymentsDataHttpService: PaymentOrdersService) {}

  private approvalsServiceCall(
    paymentId: string,
    approvalStatus: RecordStatus,
    comment?: string
  ) {
    return this.paymentsDataHttpService
      .putApprovalsByPaymentOrderId(
        {
          paymentOrderId: paymentId,
          paymentOrderApprovalPutRequest: {
            approvalStatus,
            ...(!!comment && { comment }),
          },
        },
        'response'
      )
      .pipe(map(fromResponse));
  }

  private bulkApprovalsServiceCall(
    paymentOrderIds: Array<string>,
    approvalStatus: RecordStatus,
    comment?: string
  ) {
    return this.paymentsDataHttpService
      .putBulkApprovals(
        {
          bulkPaymentOrdersApprovalPutRequest: {
            approvalStatus,
            paymentOrderIds,
            ...(!!comment && { comment }),
          },
        },
        'response'
      )
      .pipe(map(fromResponse));
  }

  loadPayments(params: LoadPaymentsParams): Observable<{
    totalCount: number;
    items: IdentifiedPaymentOrder[];
  }> {
    return this.getPaymentOrders(params as GetPaymentOrdersRequestParams).pipe(
      map((response) => {
        const totalCount = response.headers.get('x-total-count');
        return {
          totalCount: Number(totalCount),
          items: response.body || [],
        };
      })
    );
  }

  getPaymentOrders(
    params: GetPaymentOrdersRequestParams
  ): Observable<HttpResponse<IdentifiedPaymentOrder[]>> {
    return this.paymentsDataHttpService.getPaymentOrders(params, 'response');
  }

  getPayment(id: string, params: object): Observable<PaymentOrderGetResponse> {
    return this.paymentsDataHttpService
      .getPaymentOrderById(
        {
          paymentOrderId: id,
          ...params,
        },
        'response'
      )
      .pipe(map(fromResponse));
  }

  cancelPayment(
    paymentOrderId: string,
    version: number
  ): Observable<HttpResponse<PaymentOrderCancelPostResponse>> {
    return this.paymentsDataHttpService.deletePaymentOrderById({
      paymentOrderId,
      version,
    });
  }

  postPaymentCancel(
    paymentOrderId: string,
    version: number
  ): Observable<HttpResponse<PaymentOrderCancelPostResponse>> {
    return this.paymentsDataHttpService.postCancelByPaymentOrderId(
      {
        paymentOrderId,
        paymentOrderCancelPostRequest: { version },
      },
      'response'
    );
  }

  getCurrencyExchangeRate(
    currencyFrom: string,
    currencyTo: string
  ): Observable<HttpResponse<RateGet>> {
    return this.paymentsDataHttpService.getRate(
      { currencyFrom, currencyTo },
      'response'
    );
  }

  approvePayment(
    paymentId: string,
    comment?: string
  ): Observable<PaymentOrdersPostResponse> {
    return this.approvalsServiceCall(paymentId, Actions.APPROVE, comment);
  }

  bulkApprovePayments(
    paymentIds: Array<string>,
    comment?: string
  ): Observable<BulkPaymentOrdersApprovalPutResponse | undefined> {
    return this.bulkApprovalsServiceCall(paymentIds, Actions.APPROVE, comment);
  }

  rejectPayment(
    paymentId: string,
    comment?: string
  ): Observable<PaymentOrdersPostResponse> {
    return this.approvalsServiceCall(paymentId, Actions.REJECT, comment);
  }

  bulkRejectPayments(
    paymentIds: Array<string>,
    comment?: string
  ): Observable<BulkPaymentOrdersApprovalPutResponse | undefined> {
    return this.bulkApprovalsServiceCall(paymentIds, Actions.REJECT, comment);
  }

  validatePayment(
    payment: PaymentOrdersValidatePost
  ): Observable<PaymentOrdersValidatePostResponse> {
    return this.paymentsDataHttpService
      .postValidate({ paymentOrdersValidatePost: payment }, 'response')
      .pipe(map(fromResponse));
  }
}
