import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
  ErrorItem,
  IdentifiedBatchPayment,
} from '@backbase/data-ang/payment-batch';
import { StepperConfig } from '../../configs/models/form-items.model';
import { BehaviorSubject, Observable } from 'rxjs';
import { ManualBatchState, StepErrors } from '../models';
import {
  BatchOrderFromTemplatePostResponse,
  BatchTemplate,
} from '@backbase/data-ang/payment-batch-template';

@Injectable()
export class BatchOrderStateService {
  private readonly batchOrderIdSubject = new BehaviorSubject<string>('');
  private readonly formSubject = new BehaviorSubject<FormGroup>(
    new FormGroup({})
  );
  private readonly stateSubject = new BehaviorSubject<ManualBatchState>(
    ManualBatchState.INITIAL
  );
  private readonly stepSubject = new BehaviorSubject<StepperConfig | undefined>(
    undefined
  );
  private readonly errorsSubject = new BehaviorSubject<StepErrors>({});
  private readonly selectedBatchTemplateSubject = new BehaviorSubject<
    BatchTemplate | undefined
  >(undefined);
  private readonly bacthOrderFromTemplateSubject = new BehaviorSubject<
    BatchOrderFromTemplatePostResponse | undefined
  >(undefined);
  private readonly bacthPaymentsFromTemplateSubject = new BehaviorSubject<
    Array<IdentifiedBatchPayment> | undefined
  >(undefined);

  public get batchOrderId$(): Observable<string> {
    return this.batchOrderIdSubject.asObservable();
  }

  public get batchOrderId(): string {
    return this.batchOrderIdSubject.value;
  }

  public set batchOrderId(id: string) {
    this.batchOrderIdSubject.next(id);
  }

  public get form$(): Observable<FormGroup> {
    return this.formSubject.asObservable();
  }

  public get formInstance() {
    return this.formSubject.value;
  }

  public get state$(): Observable<ManualBatchState> {
    return this.stateSubject.asObservable();
  }

  public get step$(): Observable<StepperConfig | undefined> {
    return this.stepSubject.asObservable();
  }

  public get currentState(): ManualBatchState {
    return this.stateSubject.value;
  }

  public get currentStep(): StepperConfig | undefined {
    return this.stepSubject.value;
  }

  public get errors$(): Observable<StepErrors> {
    return this.errorsSubject.asObservable();
  }

  public get selectedBatchTemplate$(): Observable<BatchTemplate | undefined> {
    return this.selectedBatchTemplateSubject.asObservable();
  }

  public get selectedBatchTemplate(): BatchTemplate | undefined {
    return this.selectedBatchTemplateSubject.value;
  }

  public get batchOrderFromTemplate$(): Observable<
    BatchOrderFromTemplatePostResponse | undefined
  > {
    return this.bacthOrderFromTemplateSubject.asObservable();
  }

  public get batchPaymentsFromTemplate$(): Observable<
    Array<IdentifiedBatchPayment> | undefined
  > {
    return this.bacthPaymentsFromTemplateSubject.asObservable();
  }

  saveForm(form: FormGroup) {
    this.formSubject.next(form);
  }

  resetForm() {
    this.formSubject.next(new FormGroup({}));
  }

  setState(paymentState: ManualBatchState) {
    this.stateSubject.next(paymentState);
  }

  setStep(step: StepperConfig) {
    this.stepSubject.next(step);
  }

  resetErrors() {
    this.errorsSubject.next({});
  }

  addError(stepName: string, error: ErrorItem) {
    const errors = this.errorsSubject.value;
    const currentStepErrors = errors[stepName] || [];
    this.errorsSubject.next({
      ...errors,
      [stepName]: [...currentStepErrors, error],
    });
  }

  removeErrorAt(stepName: string, index: number) {
    const errors = this.errorsSubject.value;
    const currentStepErrors = errors[stepName] || [];
    if (currentStepErrors.length && index < currentStepErrors.length) {
      currentStepErrors.splice(index, 1);
      this.errorsSubject.next({
        ...errors,
        [stepName]: [...currentStepErrors],
      });
    }
  }

  selectBatchTemplate(batchTemplate: BatchTemplate) {
    this.selectedBatchTemplateSubject.next(batchTemplate);
  }

  unselectBatchTemplate() {
    this.selectedBatchTemplateSubject.next(undefined);
  }

  loadBatchOrderFromTemplate(batchOrder: BatchOrderFromTemplatePostResponse) {
    this.bacthOrderFromTemplateSubject.next(batchOrder);
  }

  loadBatchPaymentsFromTemplate(batchPayments: Array<IdentifiedBatchPayment>) {
    this.bacthPaymentsFromTemplateSubject.next(batchPayments);
  }

  removeBatchOrderFromTemplate() {
    this.bacthOrderFromTemplateSubject.next(undefined);
  }

  removeBatchPaymentsFromTemplate() {
    this.bacthPaymentsFromTemplateSubject.next(undefined);
  }
}
