import {
  AbstractControl,
  FormArray,
  FormGroup,
  ValidationErrors,
  ValidatorFn,
} from '@angular/forms';
import { InputFormField } from '@backbase/data-ang/payment-order-options';

import { today } from './../constants';

export function amountValidator(control: AbstractControl) {
  return control.value &&
    control.value.amount > 0 &&
    control.value.currency.length > 0
    ? undefined
    : { invalidAmount: { value: control.value } };
}

export function missingInListValidator(getIdsFn: () => string[]): ValidatorFn {
  return function (control: AbstractControl) {
    return getIdsFn().includes(control.value) || !control.value
      ? null
      : { missingInList: { value: control.value } };
  };
}

export function pastDateValidator(control: AbstractControl) {
  const selectedDate = new Date(control.value);
  return control.value &&
    today > selectedDate &&
    today.toDateString() !== selectedDate.toDateString()
    ? { pastDate: { value: control.value } }
    : null;
}

export function maxValueTwoHundredValidator(control: AbstractControl) {
  const value = parseFloat(control.value);
  return !isNaN(value) && value > 200
    ? { max: { max: 200, actual: control.value } }
    : null;
}

export function minValueTwoValidator(control: AbstractControl) {
  const value = parseFloat(control.value);
  return !isNaN(value) && value < 2
    ? { min: { min: 2, actual: control.value } }
    : null;
}

export function endDateAfterStartDateValidator(control: AbstractControl) {
  if (control.parent) {
    const parentControl = control.parent;
    const startDateControl = <AbstractControl>parentControl.get('startDate');
    const startDate = new Date(startDateControl.value);
    const endDate = new Date(control.value);
    return control.value &&
      startDate > endDate &&
      startDate.toDateString() !== endDate.toDateString()
      ? { lessThanStartDate: true }
      : undefined;
  }

  return;
}

export function sameAccountsValidator(config?: {
  creditAccountId?: string;
  debitAccountId?: string;
}): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    const parentForm = control?.parent?.parent;
    const debitAccountId = parentForm?.get(['initiator', 'id'])?.value;
    const creditAccountId = parentForm?.get(['counterparty', 'id'])?.value;
    if (control.value && debitAccountId && debitAccountId === creditAccountId) {
      return { sameDebitCreditAccountsSelected: true };
    }
    return null;
  };
}

export function scheduledPaymentValidationMethod(
  control: AbstractControl,
  _endType: string
): { required: boolean } | undefined {
  if (control.parent) {
    const parentControl = control.parent;
    const { endType, frequency } = parentControl && parentControl.value;
    return frequency.value !== 'ONCE' &&
      endType &&
      !control.value &&
      endType === _endType
      ? { required: true }
      : undefined;
  }

  return;
}

export function requiredEndDateValidator(control: AbstractControl) {
  return scheduledPaymentValidationMethod(control, 'on');
}

export function requiredAfterValidator(control: AbstractControl) {
  return scheduledPaymentValidationMethod(control, 'after');
}

export function requiredEndTypeValidator(
  control: AbstractControl
): { required: boolean } | undefined {
  if (control.parent) {
    const parentControl = control.parent;
    const {
      frequency: { value: _value },
    } = parentControl && parentControl.value;
    return !control.value && _value && _value !== 'ONCE'
      ? { required: true }
      : undefined;
  }

  return;
}

export function routingTransitNumberValidator(
  control: AbstractControl
): ValidationErrors | null {
  let { value } = control;
  const errors: ValidationErrors = {
    pattern: false,
    size: false,
  };

  if (value) {
    value = value.toString(10);
    const p = (str: string) => parseInt(str, 10);

    if (value.length !== 9) {
      errors.size = true;
    }

    const left = 3 * (p(value[0]) + p(value[3]) + p(value[6]));
    const mid = 7 * (p(value[1]) + p(value[4]) + p(value[7]));
    const right = p(value[2]) + p(value[5]) + p(value[8]);

    if ((left + mid + right) % 10 !== 0 && value.length >= 9) {
      errors.pattern = true;
    }
  }

  return errors.size || errors.pattern ? errors : null;
}

export function noWhitespaceValidator(control: AbstractControl) {
  const isWhitespace = (control.value || '').trim().length === 0;
  const isValid = !isWhitespace;
  return isValid ? null : { required: true };
}

function getSanctionedCurrencies(control: AbstractControl): Array<string> {
  const { inputFormSettings } =
    control?.parent?.parent?.get('inputFormFromSettings')?.value || {};

  return (
    inputFormSettings?.find(
      (setting: InputFormField) => setting.key === 'currency'
    )?.values || []
  );
}

export function currencyValidatorFactory(
  currencies: Array<string> = []
): ValidatorFn {
  return (control: AbstractControl) => {
    const sanctionedCurrencies = currencies.length
      ? currencies
      : getSanctionedCurrencies(control);
    if (!sanctionedCurrencies.length) {
      return null;
    }
    const pattern = `(${sanctionedCurrencies.join('|')})`;
    const regex = new RegExp(pattern);

    const isValid = regex.test(control.value && control.value.currency);
    return isValid ? null : { invalidCurrency: true };
  };
}

export function accountsValidation(rootControlName: string): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (control.root && control.parent) {
      const rootControl = control.root;
      const fromControl = <FormGroup>rootControl.get(rootControlName);
      const fromAccount = <AbstractControl>fromControl?.get('id')?.value;
      const selectedAccount = control.parent.get('id')?.value;
      return control.parent.value && fromAccount === selectedAccount
        ? { sameAccount: true }
        : null;
    }
    return null;
  };
}

export function minimumOneAccount(control: FormArray) {
  return control.controls.length > 0 ? null : { empty: true };
}

export function amountGreaterThanZero(control: AbstractControl) {
  return control.value > 0
    ? undefined
    : { invalidAmount: { value: control.value } };
}
