import {
  Component,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
} from '@angular/core';
import { NgbTooltipConfig } from '@ng-bootstrap/ng-bootstrap';

interface CardMenuItem {
  name: string;
  description?: string;
}

@Component({
  selector: 'bb-payord-card-menu',
  templateUrl: 'card-menu.component.html',
  providers: [NgbTooltipConfig],
})
export class PayordCardMenuComponent implements AfterViewInit {
  /**
   * List of tab items.
   * Default is empty list.
   */
  @Input() items: Array<CardMenuItem> = [];

  /**
   * Initial selected tab item.
   * Default is `0`.
   */
  @Input() initialSelection = 0;

  /**
   * Select event emitted when a new tab item has been selected.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() select = new EventEmitter<CardMenuItem>();

  activeItem = 0;

  constructor(config: NgbTooltipConfig) {
    config.container = 'body';
  }

  ngAfterViewInit() {
    if (this.items) {
      this.activeItem = this.items[this.initialSelection]
        ? this.initialSelection
        : 0;
    }
  }

  onSelect(index: number) {
    this.activeItem = index;
    this.select.emit(this.items[index]);
  }

  trackByName(index: number, item: CardMenuItem): string {
    return item.name;
  }
}
