import { HttpResponse } from '@angular/common/http';

import { ContactsPostRequestBody } from '@backbase/data-ang/contact-manager';
import {
  BankDetails,
  CounterPartyDetails,
} from '../../configs/models/form-items.model';
import { fromResponse } from '../helpers/http-helpers';
import { stripSpaces } from '../helpers/type-helpers';
import { SchemeNames } from '@backbase/data-ang/payment-order';
import { ContactItem, ContactList } from '../models/types';

export function contactItemFromService(contact: ContactItem) {
  return {
    name: contact.name,
    addressLine1: contact.addressLine1,
    addressLine2: contact.addressLine2,
    streetName: contact.streetName,
    town: contact.town,
    postCode: contact.postCode,
    countrySubDivision: contact.countrySubDivision,
    country: contact.country,
    accounts: contact.accounts.map((account) => ({
      ...account,
      accountNumber: account.accountNumber || account.IBAN || '',
    })),
    id: contact.id,
  };
}

export function mapContactAccounts(contact: ContactItem) {
  return contact.accounts.map((account) => ({
    name: contact.name,
    ...(contact.accounts.length > 1 ? { accountName: account.name } : null),
    addressLine1: contact.addressLine1,
    addressLine2: contact.addressLine2,
    streetName: contact.streetName,
    town: contact.town,
    postCode: contact.postCode,
    countrySubDivision: contact.countrySubDivision,
    country: contact.country,
    accounts: [
      {
        ...account,
        accountNumber: account.accountNumber || account.IBAN || '',
        email: account.email || contact.emailId,
        phoneNumber: account.phoneNumber || contact.phoneNumber,
      },
    ],
    id: contact.id,
  }));
}

export function contactListFromService(
  response: HttpResponse<ContactItem[]>,
  isLoadMore = false
): ContactList {
  const responseValue = fromResponse(response);
  const items = responseValue.reduce(
    (acc: ContactItem[], contact: ContactItem) => {
      acc.push(...mapContactAccounts(contact));
      return acc;
    },
    []
  );
  return {
    items,
    itemsCount: responseValue.length,
    count: parseInt(response.headers.get('x-total-count') as string, 10),
    isLoadMore,
  };
}

export function contactItemToService(
  counterparty: CounterPartyDetails
): ContactsPostRequestBody {
  const {
    name,
    accountNumber,
    schemeName,
    creditorBank = {},
    postalAddress = {},
  } = counterparty;
  let schemeValue;
  switch (schemeName) {
    case SchemeNames.EMAIL:
      schemeValue = { email: stripSpaces(accountNumber) };
      break;
    case SchemeNames.MOBILE:
      schemeValue = { phoneNumber: stripSpaces(accountNumber) };
      break;
    case SchemeNames.IBAN:
      schemeValue = { IBAN: stripSpaces(accountNumber) };
      break;
    default:
      schemeValue = { accountNumber: stripSpaces(accountNumber) };
  }

  return {
    name,
    addressLine1: postalAddress.addressLine1,
    addressLine2: postalAddress.addressLine2,
    streetName: postalAddress.streetName,
    town: postalAddress.town,
    postCode: postalAddress.postCode,
    countrySubDivision: postalAddress.countrySubDivision,
    country: postalAddress.country,
    accounts: [
      {
        ...getBankDetails(creditorBank),
        ...schemeValue,
      },
    ],
  };
}

export function getBankDetails({
  bankBranchCode: bankCode,
  bic: BIC,
  bankName,
  postalAddress = {},
}: BankDetails) {
  return {
    bankCode,
    BIC,
    bankName,
    bankAddressLine1: postalAddress.addressLine1,
    bankAddressLine2: postalAddress.addressLine2,
    bankStreetName: postalAddress.streetName,
    bankPostCode: postalAddress.postCode,
    bankTown: postalAddress.town,
    bankCountrySubDivision: postalAddress.countrySubDivision,
    bankCountry: postalAddress.country,
  };
}
