import { PaymentOption as PaymentOptionResponse } from '@backbase/data-ang/payment-order-options';
import { PaymentOption } from '../models';

export const mapPaymentOptions = (
  options: Array<PaymentOptionResponse>,
  optionsLabel: { [key: string]: string } = {}
): PaymentOption[] => {
  const optionsArr: PaymentOption[] = [];
  let paymentOptions;
  for (const option of options) {
    paymentOptions = option.paymentOptions || [];
    paymentOptions.forEach((paymentOption: PaymentOption) => {
      optionsArr.push({
        paymentType: option.paymentType,
        canEditCounterparty: option.canEditCounterparty,
        showHelperText: paymentOptions.length > 1,
        name: optionsLabel[option.paymentType],
        ...paymentOption,
      });
    });
  }
  return optionsArr;
};
