import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output,
} from '@angular/core';
import {
  ControlValueAccessor,
  FormControl,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { CountriesMap } from '../../constants';

@Component({
  selector: 'bb-country-selector',
  templateUrl: './country-selector.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => CountrySelectorComponent),
    },
  ],
})
export class CountrySelectorComponent implements ControlValueAccessor {
  _countries!: Array<string>;
  _selectedCountry = '';
  search = new FormControl();
  countryEntries!: Array<[string, string]>;
  filteredCountries: Array<string> = [];

  /** */
  @Input() set countries(countries: Array<string>) {
    this._countries = countries || [];
    this.setupCountries();
  }
  /** */
  @Input() selectedCountry = '';
  /** */
  @Input() helperText = '';
  /** */
  @Input() placeholder = '';
  /** */
  @Input() label = '';
  /** */
  @Input() searchPlaceholder = '';
  /** */
  @Input() dropdownIcon = 'toggle-down';
  /** */
  @Output() selectCountry = new EventEmitter<string>();

  setupCountries() {
    this.countryEntries = this._countries.map((country: string) => [
      country,
      CountriesMap[country],
    ]);
    this.filteredCountries = [...this._countries];
  }

  selectItem(country: string) {
    this.selectedCountry = country;
    this.selectCountry.emit(country);
  }

  handleSearch(query = '') {
    if (query) {
      this.filteredCountries = this.countryEntries.reduce(
        (acc: Array<string>, [key, country]) => {
          if (
            key.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1 ||
            country.toLocaleLowerCase().indexOf(query.toLocaleLowerCase()) > -1
          ) {
            acc.push(key);
          }
          return acc;
        },
        []
      );
    } else {
      this.filteredCountries = this._countries;
    }
  }

  onChange(_value: string) {}
  onTouched() {}
  writeValue(_value: string): void {}
  onBlur() {}

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
}
